import DemandwareDriver from './_base';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat, toFloat } = format;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: no
+ MaxCoupons: 10
*/

const DOMAIN_CART_URL = 'toryburch.com/on/demandware.store/Sites-ToryBurch_US-Site/default/Cart-Show';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.CART_URL = `https://www.${DOMAIN_CART_URL}`;
		this.TOTAL_SELECTOR = '.amountdue .value';
		this.CODES_ELEMENTS_SELECTOR = '.removeCouponButton';
		this.CODE_VALUE_SELECTOR = '.coupon-code-value';
		this.CART_FORM_SELECTOR = '.cart-column-right .cart-coupons-form';
		this.REQUEST_PARAMS.ADD_COUPON_PARAM_VALUE = 'Apply';
		this.SHIPPING_SELECTOR = '.cart-summary  .ordershipping .value';
		this.TAX_SELECTOR = '.cart-summary  .ordersalestax .value';
		this.DONATION_SELECTOR = '.donation-item .itemtotals .value';
	}

	async getPreTaxShippingTotal() {
		const response = await this._getUpdatedCart();
		const total = this.parseTotal(response);
		// shipping can be free
		const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
		const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response));
		const donation = [...extractElement(this.DONATION_SELECTOR, response, true)]
			.map(el => toFloat(el.textContent))
			.reduce((cur, prev) => cur + prev, 0);
		return total - shipping - tax - donation;
	}

	_getUpdatedCart() {
		return fetch(this.CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(res => res.text());
	}
};

export default {
	name: 'Tory Burch',
	domain: 'toryburch.com',
	stackable: true,
	Driver,
	showDifferentDiscountMessage: true,
	maxCoupons: 10,
	isCartEmpty() {
		return document.querySelector('.cartempty');
	},
	isOnCartUrl(href) {
		return (
			href.includes(DOMAIN_CART_URL) && document.querySelector('.discountapplied, [name="dwfrm_cart_couponCode"]')
		);
	},
};
