import DemandwareDriver from './_base';

/* BEHAVIOUR
 * One to one to template
 * Added checking isCartEmpty
 */

export default {
	name: 'Perry Ellis',
	domain: 'perryellis.com',
	stackable: true,
	cartContinueUrl: 'https://www.perryellis.com/basket',
	Driver: DemandwareDriver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.perryellis.com/basket') && document.querySelector('#dwfrm_cart_couponCode');
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
};
