import Promise from 'bluebird';
import DemandwareDriver from './_base';
import bus from '../../bus';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works:
    - Cart page
	- Checkout page (Review & Complete)
		can be with 2 URLs - one of them is similiar to cart URL

+ Is driver SPA yes
    - Code input field
    - Not empty
    - On the cart or on the right step of the checkout

+ Gather codes on site pages: no
+ Special case about the driver works (if it needed).
    - maxCoupons 5 to avoid blocking
*/

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		const TO_CHECKOUT_BTN_SELECTOR = '.checkoutButton button[value="Checkout"]';
		this.isOnCart = () => (document.querySelector(TO_CHECKOUT_BTN_SELECTOR) || {}).offsetParent;
		this.TOTAL_SELECTOR = '.order-value';
		this.SHIPPING_SELECTOR = '.shipping-method-price';
		this.TAX_SELECTOR = '.tax-estimate-values';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount > td:last-child';

		this.CODES_ELEMENTS_SELECTOR = '.coupon-code-item';
		this.CODE_VALUE_SELECTOR = '#applied-coupon-code';

		this.REQUEST_HEADERS = {
			'content-type': 'application/x-www-form-urlencoded; charset=UTF-8',
			'x-requested-with': 'XMLHttpRequest',
		};

		this.CART_FORM_SELECTOR = '.coupon-form';
		this.CART_ELEM_SELECTOR = '#shoprunnerCartContainer';

		this.PRODUCT_ITEM_SELECTOR = '.cart-row';

		this.CART_EMPTY_SELECTOR = '.cart-empty';
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode';
		this.REVIEW_STEP_SELECTOR = '.step-3.active.selected';
		this.MERCHANT_CODE_SELECTOR = '.bx-click-to-copy > span';
		this.SPINER_SELECTOR = '.loading';
	}

	checkCondition() {
		const isCartEmpty = document.querySelector(this.CART_EMPTY_SELECTOR);
		const isCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isOnCart = document.querySelector(this.CART_ELEM_SELECTOR);
		const onReviewStep = document.querySelector(this.REVIEW_STEP_SELECTOR);
		const spiner = document.querySelector(this.SPINER_SELECTOR);
		return !spiner && isCodeInput && ((!isCartEmpty && isOnCart) || onReviewStep);
	}

	beforeTestCodes() {
		this.DEFAULT_REMOVE_CODE_VALUE = this.isOnCart()
			? 'dwfrm_cart_coupons_i0_deleteCoupon'
			: 'dwfrm_singlepagecheckout_coupons_i0_deleteCoupon';
		this.REQUEST_PARAMS = {
			COUPON_PARAM_NAME: 'dwfrm_cart_couponCode',
			ADD_COUPON_PARAM_NAME: 'dwfrm_cart_addCoupon',
			ADD_COUPON_PARAM_VALUE: 'dwfrm_cart_addCoupon',
			REMOVE_COUPON_PARAM_VALUE: this.isOnCart() ? 'dwfrm_cart_addCoupon' : 'dwfrm_singlepagecheckout_addCoupon',
		};
	}

	getMerchantCodes() {
		return [
			...new Set(
				[...document.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
					.map(el => el.innerHTML.trim())
					.filter(Boolean),
			),
		];
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)]
			.filter(Boolean)
			.map(element => element.getAttribute('value'));

		return {
			codes,
			codeCount: codes.length,
		};
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice, originalItemsCount } = validationInfo;

		// Sort by discount
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];
		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code, this._requestData()).then(html => {
				this.lastResponse = html;
				const finalPrice = this.parseTotal(html);
				const finalDiscount = originalPrice - finalPrice;
				const freeGifts = this.parseItemsCount(html) - originalItemsCount;

				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
					freeGifts,
				});
				if (
					finalPrice < currentPrice ||
					// Check that we don't increase total with free gifts code
					(finalPrice === currentPrice && freeGifts > currentFreeGifts)
				) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					currentFreeGifts = freeGifts;
					return Promise.resolve();
				}
				return this.removeCode(this._removeValueName(discount.code), this._requestData()).then(
					Promise.each(successfulCodes, code => this.applyCode(code, this._requestData())),
				);
			}),
		).then(() => {
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			});
			return Promise.resolve();
		});
	}

	manageCode(requestData) {
		requestData.params.set('format', 'ajax');
		return super.manageCode(requestData);
	}

	removeCode(removeValueName, requestData) {
		requestData.params.delete('dwfrm_cart_addCoupon');
		requestData.params.delete('dwfrm_singlepagecheckout_addCoupon');
		return super.removeCode(removeValueName, requestData);
	}

	getPreTaxShippingTotal() {
		return fetch('https://www.bareminerals.com/on/demandware.store/Sites-BareMinerals_US_CA-Site/en_US/Cart-Show', {
			method: 'GET',
		})
			.then(res => res.text())
			.then(resHtml => {
				const total = this.parseTotal(resHtml);
				const shippingDiscount = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, resHtml)
					? elemToFloat(extractElement(this.SHIPPING_DISCOUNT_SELECTOR, resHtml))
					: 0;
				const shipping =
					extractElement(this.SHIPPING_SELECTOR, resHtml)
						.innerText.toLowerCase()
						.trim() === 'free'
						? 0
						: elemToFloat(extractElement(this.SHIPPING_SELECTOR, resHtml));
				const tax = extractElement(this.TAX_SELECTOR, resHtml)
					? elemToFloat(extractElement(this.TAX_SELECTOR, resHtml))
					: 0;
				return total - tax - (shipping - shippingDiscount);
			});
	}
};

export default {
	name: 'bareMinerals',
	domain: 'bareminerals.com',
	maxCoupons: 5, // server block us if we do many requests
	stackable: true,
	isSinglePageApp: true,
	Driver,
};
