import Promise from 'bluebird';
import DemandwareDriver from './_base';
import bus from '../../bus';
import { DOM, format, completionActions, log, browserUtils } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;
const { wait } = browserUtils;

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    isValid implemented: yes
    Active page: Checkout
Optional:
    Show message about free shipping on the cart page: no
    Is driver SPA and add the condition to show popup: no
    Gather codes on site pages: yes(from current page) */

const BASE_URL = 'https://www.ems.com';
const CHECKOUT_URL = `${BASE_URL}/checkout-minicart`;
const SHOPPING_CART_URL = `${BASE_URL}/shoppingcart`;
const CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.CART_FORM_SELECTOR = '#dwfrm_billing';
		this.CODE_VALUE_SELECTOR = '.promo-name strong';
		this.SUB_TOTAL_SELECTOR = '.order-subtotal td:last-child';

		this.isStackable = false;

		this.DEFAULT_REMOVE_CODE_VALUE = 'dwfrm_billing_coupons_i0_deleteCoupon';

		this.REQUEST_PARAMS = {
			COUPON_PARAM_NAME: 'dwfrm_billing_couponCode',
			ADD_COUPON_PARAM_NAME: 'dwfrm_billing_applyCoupon',
			ADD_COUPON_PARAM_VALUE: 'Apply',
			REMOVE_COUPON_PARAM_VALUE: 'Remove',
		};

		this.CODE_ERROR_MESSAGE = '.coupon-code-error';

		this.PROPOSED_CODES_SELECTOR = '[name="image-map-site"] area:last-child';
		this.MERCHANT_CODE_REGEX = /use code (\w+)/i;

		this.MERCHANT_CODE_SELECTOR = '[name="map-sitewide"] area';
	}

	async getMerchantCodes() {
		const iframeElem = document.createElement('iframe');
		iframeElem.src = '/';
		Object.assign(iframeElem.style, { display: 'none' });
		document.documentElement.appendChild(iframeElem);
		const getCodeElem = () => [...iframeElem.contentDocument.querySelectorAll(this.MERCHANT_CODE_SELECTOR)].length;
		await wait(getCodeElem, 15000, 300, true);

		if (getCodeElem()) {
			return [
				...new Set(
					[...iframeElem.contentDocument.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
						.filter(item => item.alt.match(this.MERCHANT_CODE_REGEX))
						.map(item => item.alt.match(this.MERCHANT_CODE_REGEX))
						.map(el => el[1])
						.filter(Boolean),
				),
			];
		}
		return [];
	}

	checkCodes(codes) {
		const originalPrice = this.parseTotal(this.lastResponse);
		log(`Orginal price: ${originalPrice}`);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this.applyCode(code, this._requestData()).then(applyCodeHtml => {
				this.lastResponse = applyCodeHtml;
				if (this._checkInvalidCode(applyCodeHtml)) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				result.finalPrice = this.parseTotal(applyCodeHtml);
				result.finalDiscount = originalPrice - result.finalPrice;

				return this.removeCode(this._removeValueName(code), this._requestData()).then(removeCodeHtml => {
					this.lastResponse = removeCodeHtml;
					return bus.$emit('code-checked', result);
				});
			});
		});
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedPage().then(response => {
			const subTotal = extractElement(this.SUB_TOTAL_SELECTOR, response);
			return elemToFloat(subTotal);
		});
	}

	_checkInvalidCode(html) {
		return extractElement(this.CODE_ERROR_MESSAGE, html);
	}

	_getUpdatedPage() {
		return fetch(`${BASE_URL}/billing`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Eastern Mountain Sports',
	domain: 'ems.com',
	Driver,
	cartContinueUrl: `${BASE_URL}/billing`,
	completionAction: completionActions.revisit,
	isOnCartUrl: href =>
		document.querySelector(CODE_INPUT_SELECTOR) &&
		(href.startsWith(CHECKOUT_URL) ||
			href.startsWith(`${BASE_URL}/billing`) ||
			href.includes('/COShipping-Start') ||
			href.includes(SHOPPING_CART_URL)),
};
