import Promise from 'bluebird';
import bus from '../../bus';
import DemandwareAJAXDriver from './_base';
import { DOM, format, log, completionActions } from '../../../utils';

const { elemToFloat } = format;
const { extractElementFromHTML, extractElement, extractFormValues } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: yes
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: yes
	- https://www.philosophy.com
	- https://www.philosophy.com/purity-made-simple.html (at the moment there is a valid code)
+ Restore user data: no
+ Special case: sometimes valid code that doesn't give any freegift or discount
	(because required conditions aren't achieved) can cancel discount given by previous valid code.
	Thus _invalidCodeApplied method was added to manage this case.
*/

const BASE_URL = 'https://www.philosophy.com';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CART_FORM_SELECTOR = 'form[id*="dwfrm_cart_"]:not([class*="formcheckout"])';
		this.TOKEN_SELECTOR = 'input[name="csrf_token"]';
		this.TOTAL_SELECTOR = '.ordertotal .value';
		this.ERROR_MESSAGE_SELECTOR = '.cart-coupon-code .error';
		this.SHIPPING_SELECTOR = '.ordershipping .value';
		this.TAX_SELECTOR = '.ordersalestax .value';
		this.MERCHANT_CODES_SELECTOR = '#home-promo-codes a, .mini-description strong';
		this.PRODUCT_ITEM_SELECTOR = '#cart-table .eachproductdetails';
		this.INVALID_CODE_SELECTOR = '.bonus-item';
		this.MERCHANT_CODES_REGEX = /code:\s+(\w+)/i;
		this.INVALID_CODE_REGEX = /not applied/i;
		this.isStackable = false;
	}

	async getMerchantCodes() {
		const mainPage = await this._getMainPage();
		const itemPage = await this._getItemPage();
		const promoPages = mainPage.concat(' ', itemPage);

		const codes = [...extractElement(this.MERCHANT_CODES_SELECTOR, promoPages, true)]
			.map(el => el.textContent.match(this.MERCHANT_CODES_REGEX))
			.filter(Boolean)
			.map(match => match[1]);

		return [...new Set(codes)];
	}

	checkCodes(codes) {
		const originalPrice = this.parseTotal(this.lastResponse);
		log('originalPrice: %s', originalPrice);
		const originalItemsCount = this.parseItemsCount(this.lastResponse);
		log('originalItemsCount: %s', originalItemsCount);
		return Promise.each(codes, code =>
			this.applyCode(code, this._requestData()).then(html => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: false,
				};

				if (this._codeAppliedCheck(html)) {
					return bus.$emit('code-checked', result);
				}

				if (this._invalidCodeApplied(html)) {
					return this.removeCode(this._removeValueName(code), this._requestData()).then(removeCodeHtml => {
						this.lastResponse = removeCodeHtml;
						return bus.$emit('code-checked', result);
					});
				}

				this.lastResponse = html;
				result.finalPrice = this.parseTotal(html);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this.parseItemsCount(html) - originalItemsCount;

				if (
					result.finalPrice < originalPrice ||
					// Free gifts code is acceptable if it won't increase total
					(result.finalPrice === originalPrice && result.freeGifts > 0)
				) {
					result.isValid = true;
				}

				return this.removeCode(this._removeValueName(code), this._requestData()).then(removeCodeHtml => {
					this.lastResponse = removeCodeHtml;
					return bus.$emit('code-checked', result);
				});
			}),
		);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(html => {
			const shippingElement = extractElement(this.SHIPPING_SELECTOR, html);
			const shipping = shippingElement ? elemToFloat(shippingElement) || 0 : 0;
			const taxElement = extractElement(this.TAX_SELECTOR, html);
			const tax = taxElement ? elemToFloat(taxElement) : 0;
			return this.parseTotal(html) - shipping - tax;
		});
	}

	_getUpdatedCart() {
		return this._getApi(this._requestData().action);
	}

	_getMainPage() {
		return this._getApi(BASE_URL);
	}

	_getItemPage() {
		return this._getApi(`${BASE_URL}/purity-made-simple.html`);
	}

	_getApi(url) {
		return fetch(url, { method: 'GET', credentials: 'include' }).then(response => response.text());
	}

	_requestData() {
		// Token is not present in the form, need to add it manually to use template
		const csrfTokenElement = this.lastResponse
			? extractElementFromHTML(this.lastResponse, this.TOKEN_SELECTOR)
			: document.querySelector(this.TOKEN_SELECTOR);

		const formValues = extractFormValues(this.CART_FORM_SELECTOR, this.lastResponse);
		formValues.params.set('csrf_token', csrfTokenElement.value);
		return formValues;
	}

	_invalidCodeApplied(html) {
		return (
			extractElement(this.INVALID_CODE_SELECTOR, html) &&
			this.INVALID_CODE_REGEX.test(extractElement(this.INVALID_CODE_SELECTOR, html).textContent)
		);
	}
};

export default {
	name: 'Philosophy',
	domain: 'philosophy.com',
	completionAction: completionActions.revisit,
	Driver,
	isOnCartUrl(href) {
		return document.querySelector('#dwfrm_cart_couponCode') && href.startsWith(`${BASE_URL}/cart`);
	},
};
