import DemandwareDriver from './_base';
import { completionActions } from '../../../utils';
/*
Behavior description:
- can get existing codes;
- can remove codes;
- valid code can't be replaced with another valid code;
- can't apply multiple codes (not stackable);
- code testing may be run only on 'Cart' page;
======== Special case ========
we have code input on billing page but we can't apply code manually 
because the behavior of the site will clear all of the user's data
so we don't support billing page
*/

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
	}
};

export default {
	name: 'A Pea In The Pod',
	domain: 'apeainthepod.com',
	stackable: true,
	Driver,
	completionAction: completionActions.revisit,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return (
			(href.startsWith('https://www.apeainthepod.com/cart') ||
				href.startsWith(
					'https://www.apeainthepod.com/on/demandware.store/Sites-pea-us-Site/default/Cart-AddToWishlist',
				)) &&
			document.querySelector('#dwfrm_cart_couponCode')
		);
	},
};
