import DemandwareDriver from './_base';
import { DOM, completionActions, format } from '../../../utils';
import { sessionTimer } from '../../lib';

const { elemToFloat } = format;
const { extractElement } = DOM;

/**
 * Driver behavior description:
 -    Existing code: yes
 -    Remove code: yes
 -    Stackable: no
 -    Free gift support: no
 -    isValid implemented: no
 -    Valid code can be replaced with another valid code: no
 -    Manually apply code: no
 -	  sessionTimer 15 min
 - 	  code testing may be run on 'Cart' page;
 =========Special case=========
 If u come on checkout step choose city with paid shipping and tax then coming back to cart price will include tax, but responses will not include. 
 So in this case original price in extension will shown without tax and shipping and discount will shown correct.
 */

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .price-value';
		this.CODE_MESSAGE_SELECTOR = '.code-message';
		this.INPUT_CODE_SELECTOR = '#dwfrm_cart_couponCode';
		this.CODE_VALUE_SELECTOR = '.code-applied .code-number';
		this.SHIPPING_SELECTOR = '.order-shipping .price-value';
		this.TAX_SELECTOR = '.order-sales-tax';
		this.PRICES_SELECTOR = '.order-subtotal';
		this.PRICE_SELECTOR = '.price-value';

		this.isStackable = false;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const inputCode = document.querySelector(this.INPUT_CODE_SELECTOR);
		const codeMsg = document.querySelector(this.CODE_MESSAGE_SELECTOR);
		const total = document.querySelector(this.TOTAL_SELECTOR);
		const isTotal = total && total.offsetParent;
		const currentState = (inputCode || codeMsg) && isTotal;
		return currentState;
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.CODE_VALUE_SELECTOR)]
			.filter(Boolean)
			.map(element => element.textContent.trim());

		return {
			codes,
			codeCount: codes.length,
		};
	}

	async beforeTestCodes() {
		this.lastResponse = await this._getPage();
	}

	getPreTaxShippingTotal() {
		return this._getPage().then(responseHTML => {
			const total = this.parseTotal(responseHTML);
			const donationEl = this._findElement('Donation', responseHTML);
			const donation = donationEl ? elemToFloat(donationEl) || 0 : 0;
			const shipping = parseFloat(extractElement(this.SHIPPING_SELECTOR, responseHTML).textContent || 0) || 0;
			const taxes = elemToFloat(extractElement(this.TAX_SELECTOR, responseHTML) || 0) || 0;
			return total - donation - shipping - taxes;
		});
	}

	_findElement(targetString, response) {
		const targetEl = [...extractElement(this.PRICES_SELECTOR, response, true)].find(el =>
			el.textContent.includes(targetString),
		);
		return targetEl ? targetEl.querySelector(this.PRICE_SELECTOR) : null;
	}

	_getPage() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	Driver,
	name: 'Brooks Brothers',
	domain: 'brooksbrothers.com',
	completionAction: completionActions.revisit,
	isSinglePageApp: true,
};
