import DemandwareDriver from './_base';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';

		this.ERROR_MESSAGE_SELECTOR = '.cart-coupon-code .error';
	}

	applyCodes(codes) {
		window.history.pushState(window.history.state, window.document.title, window.location.href.split('?')[0]);
		return super.applyCodes(codes);
	}
};

export default {
	name: 'Cubavera',
	domain: 'cubavera.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			href.startsWith('https://www.cubavera.com/basket') &&
			document.querySelector('[name="dwfrm_cart_couponCode"]')
		);
	},
};
