import DemandwareDriver from './_base';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
	}
};

export default {
	name: 'Icing',
	domain: 'icing.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.icing.com/us/shopping-cart');
	},
};
