import DemandwareDriver from './_base';

import { format, DOM } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable:  yes
    Free gift support: no
    isValid implemented:  no
    Gather codes: yes
    Can run codes testing only on Cart
Optional:
    Show message about free shipping on the cart page (without discount info): no
    Is driver SPA and add the condition to show popup: no
*/

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.BASE_URL = 'https://www.dcshoes.com';
		this.TOTAL_SELECTOR = '.total-value-container .ordertotal';
		this.CODE_VALUE_SELECTOR = '.cartcoupon-code';
		this.CART_FORM_SELECTOR = '#dwfrm_cart-tableform';

		this.SUMMARY_ITEMS_SELECTOR = '.total-label-container .total-set';
		this.TAX_VALUE_SELECTOR = '.total-value-container .total-set:nth-last-child(2)';
		this.SHIPPING_VALUE_SELECTOR = '.total-value-container .total-set:nth-child(2)';
		this.SHIPPING_WITH_DISCOUNT_SELECTOR = '.total-value-container .total-set:nth-child(3)';
		this.ERROR_MESSAGE_SELECTOR = '.cart__couponerrors';
		this.CART_ITEM_SELECTOR = '.cart__item';
		this.MERCHANT_CODE_SELECTOR = '.coupon';

		this.isStackable = true;
	}

	checkCondition() {
		const { href } = window.location;
		const isOnCart =
			href.includes(`${this.BASE_URL}/cart`) ||
			href.includes(`${this.BASE_URL}/on/demandware.store/Sites-DC-US-Site/en_US/Cart-SubmitForm`);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const item = document.querySelector(this.CART_ITEM_SELECTOR);
		return isOnCart && isTotal && item;
	}

	getMerchantCodes() {
		return [
			...new Set(
				[...document.querySelectorAll(this.MERCHANT_CODE_SELECTOR)].map(el => el.innerText).filter(Boolean),
			),
		];
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(element => element.innerText.trim()),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	beforeApplyCodes() {
		window.history.replaceState(window.history.state, window.document.title, `${this.BASE_URL}/cart`);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const tax = [...extractElement(this.SUMMARY_ITEMS_SELECTOR, responseHTML, true)].filter(el =>
				el.textContent.includes('Tax'),
			);
			const shipping = [...extractElement(this.SUMMARY_ITEMS_SELECTOR, responseHTML, true)].filter(el =>
				el.textContent.includes('Shipping'),
			);
			const orderDiscount = [...extractElement(this.SUMMARY_ITEMS_SELECTOR, responseHTML, true)].filter(el =>
				el.textContent.includes('Discount'),
			);
			const taxValue = tax[0] ? elemToFloat(extractElement(this.TAX_VALUE_SELECTOR, responseHTML)) || 0 : 0;
			const shippingValue = shipping[0]
				? elemToFloat(
						extractElement(
							orderDiscount[0] ? this.SHIPPING_WITH_DISCOUNT_SELECTOR : this.SHIPPING_VALUE_SELECTOR,
							responseHTML,
						),
				  ) || 0
				: 0;
			const totalValue = this.parseTotal(responseHTML);
			return totalValue - taxValue - shippingValue;
		});
	}

	_getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'DC SHOES',
	domain: 'dcshoes.com',
	isSinglePageApp: true,
	Driver,
	stackable: true,
};
