import DemandwareDriver from './_base';
import completionActions from '../../../utils/completion-actions';
import { DOM } from '../../../utils';

const { extractElement } = DOM;

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .td-totals';
		this.CODE_VALUE_SELECTOR = '.item-details > .name';
		this.PROMO_CODE_REGEX = /Promo ([^ ]+)/i;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(
			element => element.innerText.match(this.PROMO_CODE_REGEX)[1],
		);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	_removeValueName(code) {
		const codesElements = extractElement(this.CODES_ELEMENTS_SELECTOR, this.lastResponse, true);
		const matchingCodesElement = [...codesElements].find(element => {
			const codesElement = element.querySelector(this.CODE_VALUE_SELECTOR);
			return (
				codesElement &&
				codesElement.innerText.match(this.PROMO_CODE_REGEX)[1].toLowerCase() === code.toLowerCase()
			);
		});
		return matchingCodesElement
			? matchingCodesElement.querySelector(this.REMOVE_CODE_SELECTOR).name
			: this.DEFAULT_REMOVE_CODE_VALUE;
	}
};

export default {
	name: 'Kenneth Cole',
	domain: 'kennethcole.com',
	stackable: true,
	completionAction: completionActions.revisit,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.kennethcole.com/cart') && document.querySelector('#cart-items-form');
	},
};
