import DemandwareDriver from './_base';
import completionActions from '../../../utils/completion-actions';

/* BEHAVIOUR
 * Can apply code only on cart page
 * One to one with base driver
 */

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.isStackable = false;
	}
};

export default {
	name: 'Bowflex',
	domain: 'bowflex.com',
	completionAction: completionActions.revisit,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.bowflex.com/cart') && document.querySelector('#dwfrm_cart_couponCode');
	},
};
