import Promise from 'bluebird';
import DemandwareDriver from './_base';
import { format } from '../../../utils';
import { sessionTimer } from '../../lib';
import { extractElement } from '../../../utils/DOM';
import { elemToFloat } from '../../../utils/format';

const { toFloat } = format;
/**
 * Driver behavior description:
 -    Existing code: yes
 -    Remove code: yes
 -    Stackable: yes
 -    Free gift support: yes
 -    isValid implemented: yes
 -    Manually apply code:no
 -    Is driver SPA and add the condition to show popup: yes
 -	sessionTimer 15 min
 */
const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '#estimatedTotal';

		this.CODES_ELEMENTS_SELECTOR = '.couponAppliedItem';
		this.CODE_VALUE_SELECTOR = '.promoCuponCode';
		this.REMOVE_VALUE_ATTRIBUTE = 'data-uuid';

		this.REMOVE_CODE_SELECTOR = '.removecoup';
		this.EMPTY_CART_SELECTOR = '#emptyCart';
		this.CART_PAGE = 'https://www.crocs.com/on/demandware.store/Sites-crocs_us-Site/default/Cart-Show';

		this.CHARGES_REGEX = /(checkout|tax)/i;
		this.CHARGES_SELECTOR = '.cart-footer-table .child-padding>div:last-child';
	}

	checkCondition() {
		const { href } = window.location;

		const checkoutURL = href.startsWith(this.CART_PAGE);
		const emptyCart = Boolean(document.querySelector(this.EMPTY_CART_SELECTOR));

		return checkoutURL && !emptyCart;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	beforeTestCodes() {
		this.bodyData = new URLSearchParams({
			updates: 'approaching|promos|shipping|delivery',
		});

		return Promise.resolve();
	}

	applyCode(code) {
		this.bodyData.set('action', 'addcoupon');
		this.bodyData.set('coupon', code);
		this.bodyData.delete('uuid');
		return this.manageCode();
	}

	removeCode(removeValueUuid) {
		this.bodyData.set('uuid', removeValueUuid);
		this.bodyData.set('action', 'removecoupon');
		this.bodyData.delete('coupon');
		return this.manageCode();
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this._getUpdatedCart();
		const total = elemToFloat(extractElement(this.TOTAL_SELECTOR, cartStr));
		const taxes = [...extractElement(this.CHARGES_SELECTOR, cartStr.summary, true)]
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(elemToFloat)
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0); // shipping & tax might not be counted on cart
		return total - taxes;
	}

	_getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_removeValueName(code) {
		let removeValueUuid = null;

		if (this.lastResponse) {
			const codesObjects = this.lastResponse.data.cartPromos.couponItemPromos;
			for (let i = 0; i < codesObjects.length; i++) {
				const codesObject = codesObjects[i];
				if (codesObject && codesObject.couponCode === code) {
					removeValueUuid = codesObjects[i].uuid;
					break;
				}
			}
		} else {
			const codesElements = Array.from(document.querySelectorAll(this.CODES_ELEMENTS_SELECTOR));
			const matchingCodesElement = codesElements.find(element => {
				const codesElement = element.querySelector(this.CODE_VALUE_SELECTOR);
				return codesElement && codesElement.innerText.trim() === code;
			});
			if (matchingCodesElement) {
				removeValueUuid = matchingCodesElement
					.querySelector(this.REMOVE_CODE_SELECTOR)
					.getAttribute(this.REMOVE_VALUE_ATTRIBUTE);
			}
		}
		return removeValueUuid;
	}

	manageCode() {
		return fetch('https://www.crocs.com/on/demandware.store/Sites-crocs_us-Site/default/Cart-API', {
			method: 'POST',
			body: this.bodyData,
			credentials: 'include',
		}).then(response => response.json());
	}

	parseTotal(response) {
		if (response) {
			return toFloat(response.data.cartEstimatedTotal);
		}
		return toFloat(document.querySelector(this.TOTAL_SELECTOR).innerText);
	}
};

export default {
	name: 'Crocs',
	domain: 'crocs.com',
	stackable: true,
	Driver,
	isSinglePageApp: true,
};
