import DemandwareDriver from './_base';
import { format, DOM } from '../../../utils';

const { elemToFloat, toFloat } = format;
const { extractElement } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: yes, from Cart page
*/

const CART_URL = 'https://www.dooney.com/cart';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.PROPOSED_CODES_SELECTOR = '.top-promo-banner a';
		this.CODE_REGEX = /(code\s+|code:\s+)(\w+)/g;
		this.PRODUCT_ITEM_SELECTOR = '.cart-row';
		this.ERROR_MESSAGE_SELECTOR = '.error';
		this.TAX_SELECTOR = '.order-sales-tax td:nth-child(2)';
		this.SHIPPING_SELECTOR = '.order-shipping td:nth-child(2)';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:nth-child(2)';
	}

	getMerchantCodes() {
		return this._getUpdatedCart().then(responseHTML =>
			[...extractElement(this.PROPOSED_CODES_SELECTOR, responseHTML, true)]
				.map(el => el.textContent.match(this.CODE_REGEX))
				.filter(Boolean)
				.map(el => el[0].split(' ').pop()),
		);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscount = shippingDiscountEl ? toFloat(shippingDiscountEl.textContent.replace('-', '')) : 0;
			return this.parseTotal(response) - tax - shipping + shippingDiscount;
		});
	}

	_getUpdatedCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Dooney & Bourke',
	domain: 'dooney.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('#cart-coupon-code');
	},
	isCartEmpty() {
		return (document.querySelector('.cart-empty') || {}).offsetParent;
	},
};
