import DemandwareAJAXDriver from './_base';
import { sessionTimer } from '../../lib';
import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: yes, from main page
+ SessionTimer: 15 min
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.BASE_URL = 'https://www.shoecarnival.com';
		this.CODES_SELECTOR = '.rowcoupons .cartcoupon .value';
		this.ERROR_MESSAGE_SELECTOR = '.cart-coupon-code .error';
		this.MERCHANT_CODES_SELECTOR = '.home-flex-banner picture img, .home-bottom-slots picture img';
		this.MERCHANT_CODES_REGEXP = /code:\s([\d\w]+)/i;
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount.discount td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isOnCart = href.startsWith(`${this.BASE_URL}/cart`) || href.includes('Cart-SubmitForm');
		const isTotal = (document.querySelector(this.TOTAL_SELECTOR) || {}).offsetParent;
		return isOnCart && isTotal;
	}

	getMerchantCodes() {
		return this._getHomePage().then(responseHTML => [
			...new Set(
				[...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
					.map(elem => elem.getAttribute('alt').match(this.MERCHANT_CODES_REGEXP))
					.filter(Boolean)
					.map(code => code[1]),
			),
		]);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODES_SELECTOR)].map(el => el.textContent.trim());

		return {
			codes,
			codeCount: codes.length,
		};
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this._getUpdatedCart();
		const total = this.parseTotal(cartStr);
		const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, cartStr)) || 0;
		const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, cartStr);
		const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) : 0;
		const tax = elemToFloat(extractElement(this.TAX_SELECTOR, cartStr)) || 0;
		return total - shipping - tax + shippingDiscount;
	}

	_getHomePage() {
		return this._getPage('home');
	}

	_getUpdatedCart() {
		return this._getPage('cart');
	}

	_getPage(endPoint = '') {
		return fetch(`${this.BASE_URL}/${endPoint}`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Shoe Carnival',
	domain: 'shoecarnival.com',
	stackable: true,
	isSinglePageApp: true,
	Driver,
};
