import DemandwareDriver from './_base';
import { completionActions, format } from '../../../utils';

const { elemToFloat } = format;

/* Behavior description:
- existing code: yes
- remove code: yes
- stackable: no
- free gift support: no
- free shipping: no
- 'isValid' implemented: yes
- manually apply code: no
- replace code: no
- Pages, where extension works: Cart page and Checkout page on Billing step
- SPA: no
- gather codes on site pages: no
- credentials (joseph@gmail.com, 12345qwerty)
*/

const BASE_URL = 'https://www.brookstone.com';
const CART_URL = `${BASE_URL}/cart`;
const CHECKOUT_URL = `${BASE_URL}/billing`;

const CART_FORM_SELECTOR = 'form[name="dwfrm_cart"]';
const CHECKOUT_FORM_SELECTOR = '#dwfrm_billing';
const TOTAL_SELECTOR = '.order-total .order-totals-value';

// For this merchant it is safer to check form existence,
// because URL paths may vary for same pages
const TOTAL_ELEM = document.querySelector(TOTAL_SELECTOR);
const isOnCartPage = document.querySelector(CART_FORM_SELECTOR);
const isOnCheckoutPage = document.querySelector(CHECKOUT_FORM_SELECTOR);

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = TOTAL_SELECTOR;
		this.CODE_VALUE_SELECTOR = `${CART_FORM_SELECTOR} .success-code-text, #promo-code-fields .coupon-container .text-container`;
		this.CODE_REGEX = /Promo Code\s*([^ ]+)/;
		this.ERROR_MESSAGE_SELECTOR = `${CART_FORM_SELECTOR} .error`;
		this.isStackable = false;
	}

	beforeTestCodes() {
		// for Checkout page need to get form from Cart page
		if (!isOnCartPage) {
			return this._getCart().then(response => {
				this.lastResponse = response;
				return Promise.resolve();
			});
		}
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)]
			.filter(Boolean)
			.filter(el => el.textContent.match(this.CODE_REGEX))
			.map(el => el.textContent.match(this.CODE_REGEX)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	// eslint-disable-next-line class-methods-use-this
	_getCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Brookstone',
	domain: 'brookstone.com',
	cartContinueUrl: isOnCartPage ? window.location.href : CHECKOUT_URL,
	completionAction: completionActions.revisit,
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl() {
		return (isOnCartPage || isOnCheckoutPage) && TOTAL_ELEM && elemToFloat(TOTAL_ELEM);
	},
};
