import DemandwareDriver from './_base';

/* BEHAVIOUR
 * Can apply more than one code at the same time (stackable)
 * Can remove on cart page
 * Can getExistingCodes
 */

export default {
	name: 'La Senza US',
	domain: 'lasenza.com',
	// can't check it, only one code
	stackable: true,
	Driver: DemandwareDriver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.lasenza.com/us/checkout/cart');
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
};
