import Promise from 'bluebird';
import DemandwareDriver from './_base';
import bus from '../../bus';
import { DOM, format, log } from '../../../utils';
import { handleDriverError, sessionTimer } from '../../lib';

const { extractFormValues, extractElement } = DOM;
const { elemToFloat } = format;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: no
+ session timer: 20 min
+ if during testing Cart becomes empty send error to the Sentry for further investigation
*/

const BASE_URL = 'https://www.ashleyfurniture.com';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.TOTAL_ELEMENT_CONFIG = { selector: this.TOTAL_SELECTOR };
		this.TOKEN_SELECTOR = '[name="csrf_token"]';
		this.CODE_VALUE_SELECTOR = '.cartcoupon .coupon-code';
		this.CART_FORM_SELECTOR = '#cart-items-form';
		this.PRODUCT_ITEM_SELECTOR = '.line-item-wrapper:not(.visually-hidden)';
		this.CART_EMPTY_SELECTOR = '.cart-empty';
		this.ITEM_LINK_SELECTOR = '.line-item-wrapper .name  a';
		this.ITEM_QTY_SELECTOR = '.line-item-wrapper .item-quantity [selected]';

		this.PROPOSED_CODES_SELECTOR = '.header-promo-container .trisectional-banner-inner';
		this.PROPOSED_CODES_REGEX = /code: (\w+)/i;
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax-value';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.CART_URL = `${BASE_URL}/cart`;

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 20,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const totalEl = document.querySelector(this.TOTAL_SELECTOR);
		const isOnRightUrl =
			href.startsWith(this.CART_URL) ||
			(href.startsWith(BASE_URL) && href.includes('/Cart-SubmitForm')) ||
			href.startsWith(`${BASE_URL}/check-out`);

		return !isCartEmpty && isOnRightUrl && totalEl && !totalEl.textContent.includes('N/A');
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.PROPOSED_CODES_SELECTOR)]
			.map(el => el.textContent.match(this.PROPOSED_CODES_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
	}

	getPreTaxShippingTotal() {
		return this._getCartSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response));
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response));
			return this.parseTotal(response) - (shipping || 0) - (tax || 0);
		});
	}

	manageCode(requestData) {
		let responseStatus = null;
		return Promise.delay(1000).then(() =>
			fetch(requestData.action, {
				method: 'POST',
				body: requestData.params,
				credentials: 'include',
				headers: this.REQUEST_HEADERS,
			})
				.then(response => {
					responseStatus = response.status;
					return response.text();
				})
				.then(response => {
					if (!this.parseItemsCount(response)) {
						return this._showDriverError(responseStatus, response, requestData.params).then(() => null);
					}
					return response;
				}),
		);
	}

	_showDriverError(status, response, body) {
		return this._getCartSummary().then(responseHTML => {
			if (extractElement(this.CART_EMPTY_SELECTOR, responseHTML)) {
				const extraInfo = {
					...this._parseItemsInfo(),
					manageCodeStatus: status,
					manageCodeResponse: response,
					body,
				};
				handleDriverError(new Error('Cart is empty'), extraInfo);
			}
		});
	}

	_parseItemsInfo() {
		return {
			items: [...document.querySelectorAll(this.PRODUCT_ITEM_SELECTOR)].map(el => {
				return {
					href: el.querySelector(this.ITEM_LINK_SELECTOR).getAttribute('href'),
					qty: el.querySelector(this.ITEM_QTY_SELECTOR).getAttribute('value'),
				};
			}),
		};
	}

	_getCartSummary() {
		return fetch(this.CART_URL, {
			method: 'GET',
		}).then(response => response.text());
	}

	_requestData() {
		// Token is not present in the form, need to add it manually to use template
		const csrfTokenElement = extractElement(this.TOKEN_SELECTOR, this.lastResponse);
		const formValues = extractFormValues(this.CART_FORM_SELECTOR, this.lastResponse);
		formValues.params.set('csrf_token', csrfTokenElement.value);
		formValues.params.set('dwfrm_cart_updateCart', 'dwfrm_cart_updateCart');
		formValues.params.delete('inventory');
		return formValues;
	}

	checkCodes(codes) {
		const originalPrice = this.parseTotal(this.lastResponse);
		log('originalPrice: %s', originalPrice);
		const originalItemsCount = this.parseItemsCount(this.lastResponse);
		log('originalItemsCount: %s', originalItemsCount);
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code, this._requestData())
				.then(() => this._getCartSummary())
				.then(html => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
						freeGifts: 0,
						isValid: true,
					};

					this.lastResponse = html;
					result.finalPrice = this.parseTotal(html);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(html) - originalItemsCount;
					result.isValid = originalPrice > result.finalPrice;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}

					return this.removeCode(this._removeValueName(code), this._requestData())
						.then(() => this._getCartSummary())
						.then(removeCodeHtml => {
							this.lastResponse = removeCodeHtml;
							return bus.$emit('code-checked', result);
						});
				}),
		).then(() => {
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			});
			return Promise.resolve();
		});
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice, originalItemsCount } = validationInfo;

		// Sort by discount
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];
		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code, this._requestData())
				.then(() => this._getCartSummary())
				.then(html => {
					this.lastResponse = html;
					const finalPrice = this.parseTotal(html);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this.parseItemsCount(html) - originalItemsCount;

					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
						freeGifts,
					});
					if (
						finalPrice < currentPrice ||
						// Check that we don't increase total with free gifts code
						(finalPrice === currentPrice && freeGifts > currentFreeGifts)
					) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						currentFreeGifts = freeGifts;
						return Promise.resolve();
					}
					return this.removeCode(this._removeValueName(discount.code), this._requestData())
						.then(() => this._getCartSummary())
						.then(removeCodeHtml => {
							this.lastResponse = removeCodeHtml;
							return Promise.resolve();
						});
				}),
		).then(() => {
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			});
			return Promise.resolve();
		});
	}

	removeCodes(codes) {
		return Promise.each(codes, code =>
			this.removeCode(this._removeValueName(code), this._requestData())
				.then(() => this._getCartSummary())
				.then(html => {
					this.lastResponse = html;
					return Promise.resolve();
				}),
		);
	}
};

export default {
	name: 'Ashley Homestore',
	domain: 'ashleyfurniture.com',
	stackable: true,
	Driver,
	cartContinueUrl: `${BASE_URL}/cart`,
	showDifferentDiscountMessage: true,
	isSinglePageApp: true,
};
