import DemandwareDriver from './_base';

const BASE_URL = 'https://www.1800mattress.com';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
	}
};

export default {
	name: '1800Mattress',
	domain: '1800mattress.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			href.startsWith(`${BASE_URL}/cart`) ||
			href.startsWith(`${BASE_URL}/on/demandware.store/Sites-1800Mattress-RV-Site/default/Cart-SubmitForm`)
		);
	},
};
