import Promise from 'bluebird';
import DemandwareDriver from './_base';
import bus from '../../bus';
import { DOM, format, log, completionActions } from '../../../utils';

const { toFloat } = format;
const { extractElement } = DOM;

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: yes
    Free gift support: yes
    isValid implemented: no
    Manually apply code: no
Optional:
    Show message about free shipping on the cart page (without discount info): no
    Is driver SPA and add the condition to show popup: no
    Gather codes on site pages: no */

const DOMAIN = 'https://cottonon.com';
const BASE_URL = `${DOMAIN}/on/demandware.store/Sites-cog-us-Site/en_US`;
const CART_URL = `${DOMAIN}/US/bag/`;

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.row.order-total-row .order-total';
		this.ITEM_SELECTOR = '.primary-content';

		this.TOTAL_ELEMENT_CONFIG = {
			selector: this.TOTAL_SELECTOR,
		};

		this.CODES_ELEMENTS_SELECTOR = '.row.promo-code-row';
		this.CODE_VALUE_SELECTOR = '.promo-code-item';
		this.CART_FORM_SELECTOR = '#cart-items-form';

		this.REQUEST_PARAMS = {
			COUPON_PARAM_NAME: 'promoCode',
		};

		this.applyData = {
			action: `${BASE_URL}/Cart-AddPromoCode`,
			params: new URLSearchParams(),
		};

		this.removeData = {
			action: `${BASE_URL}/Cart-RemovePromoCode`,
			params: new URLSearchParams(),
		};

		this.CSRF_TOKEN_SELECTOR = '[name="csrf_token"]';
		this.PRODUCT_ITEM_SELECTOR = '.cart-product-row';
		this.lastResponse = false;
	}

	checkCondition() {
		const { href } = window.location;
		const isRightUrl =
			href.includes(`${DOMAIN}/US`) && (href.includes('/bag/') || href.includes('/Cart-SubmitForm'));
		const isCartEmptyEl = document.querySelector(this.ITEM_SELECTOR);
		const isCartEmpty = isCartEmptyEl
			? isCartEmptyEl.innerText.trim().match(/empty|page no longer exists/i)
			: false;
		return isRightUrl && !isCartEmpty;
	}

	beforeTestCodes() {
		this.applyData.params.set('csrf_token', document.querySelector(this.CSRF_TOKEN_SELECTOR).value);
		this.removeData.params = this.applyData.params;
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(element =>
				element.innerText.trim().toUpperCase(),
			),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this.removeCode(code)).then(() => this._getUpdatedCart());
	}

	checkCodes(codes) {
		const originalPrice = this.parseTotal();
		log('originalPrice: %s', originalPrice);
		const originalItemsCount = this.parseItemsCount();
		log('originalItemsCount: %s', originalItemsCount);
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(json => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: true,
				};
				if (!json.success) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				return this._getUpdatedCart().then(() => {
					result.finalPrice = this.parseTotal();
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount() - originalItemsCount;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() =>
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			}),
		);
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice } = validationInfo;

		// Sort by discount
		discounts.sort((a, b) => b.finalDiscount - a.finalDiscount);
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];
		return Promise.each(discounts, discount =>
			this.applyCode(discount.code).then(() =>
				this._getUpdatedCart().then(() => {
					const finalPrice = this.parseTotal();
					const finalDiscount = originalPrice - finalPrice;
					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
					});
					if (finalPrice < currentPrice) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						return Promise.resolve();
					}
					return this.removeCode(discount.code);
				}),
			),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			}),
		);
	}

	applyCode(code) {
		this.applyData.params.set(this.REQUEST_PARAMS.COUPON_PARAM_NAME, code);
		return this.manageCode(this.applyData);
	}

	removeCode(code) {
		this.removeData.params.set(this.REQUEST_PARAMS.COUPON_PARAM_NAME, code);
		return this.manageCode(this.removeData);
	}

	manageCode(requestData) {
		return fetch(requestData.action, {
			method: 'POST',
			body: requestData.params,
			credentials: 'include',
			headers: this.REQUEST_HEADERS,
		}).then(response => response.json());
	}

	_getUpdatedCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		})
			.then(response => response.text())
			.then(html => {
				this.lastResponse = html;
			});
	}

	parseTotal() {
		return toFloat(extractElement(this.TOTAL_SELECTOR, this.lastResponse).textContent.replace('US', ''));
	}

	parseItemsCount() {
		return extractElement(this.PRODUCT_ITEM_SELECTOR, this.lastResponse, true).length;
	}
};

export default {
	name: 'CottonOn',
	domain: 'cottonon.com',
	stackable: true,
	completionAction: completionActions.revisit,
	isOriginalCaseCodes: true,
	Driver,
	isSinglePageApp: true,
	isCartEmpty() {
		return Boolean(
			document
				.querySelector('.primary-content')
				.innerText.trim()
				.match(/empty|page no longer exists/i),
		);
	},
};
