import DemandwareDriver from './_base';
import { completionActions, format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

const BASE_SITE_URL = 'https://www.ganderoutdoors.com';
const CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode, form .cart-coupon-code';
const CART_PAGE_URL = `${BASE_SITE_URL}/cart`;
const isOnCart = window.location.href.startsWith(CART_PAGE_URL);

/**
 * Driver behavior description:
 -    Existing code: yes, but not always
 -    Remove code: yes
 -    Stackable: no
 -    Free gift support: no
 -    isValid implemented: no
 Optional:
 -    Is driver SPA and add the condition to show popup: no
 * Code can be replaced with another valid code
 * Code can't be removed or replaced with empty code
 * Can run testing on cart and Payment pages
 */

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.estimated-price, .order-total td:last-child';
		this.APPLIED_CODES_SELECTOR = '.cart-footer .coupon-apply-title, .coupon .success';
		this.CART_FORM_SELECTOR = '[name="dwfrm_cart"]';
		this.CODES_REGEX = /code\s(\w+)/;
		this.MERCHANTS_CODE_SELECTOR = '.bx-row-coupon-default .coupon';
		this.isStackable = false;

		this.SHIPPING_SELECTOR = '.order-shipping .right-col';
		this.TAX_SELECTOR = '.order-sales-tax .right-col';
	}

	beforeTestCodes() {
		if (isOnCart) {
			return Promise.resolve();
		}
		// need to send request for getting data for checkout
		return this._getPage();
	}

	getMerchantCodes() {
		return [
			...new Set(
				[...document.querySelectorAll(this.MERCHANTS_CODE_SELECTOR)]
					.map(elem => elem.textContent.trim())
					.filter(Boolean),
			),
		];
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(element => (isOnCart ? element.textContent.trim() : element.textContent.match(this.CODES_REGEX)))
			.map(code => (isOnCart ? code : code[1]))
			.filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeCheckCodes() {
		return this.removeCode(this.DEFAULT_REMOVE_CODE_VALUE, this._requestData()).then(() =>
			this._getPage(CART_PAGE_URL),
		);
	}

	getPreTaxShippingTotal() {
		return this._getPage().then(responseHTML => {
			const shippingElem = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const taxElem = extractElement(this.TAX_SELECTOR, responseHTML);
			const shipping = shippingElem ? elemToFloat(shippingElem) || 0 : 0;
			const taxes = taxElem ? elemToFloat(taxElem) || 0 : 0;
			return this.parseTotal(responseHTML) - shipping - taxes;
		});
	}

	_getPage(url = CART_PAGE_URL) {
		return fetch(url, {
			method: 'GET',
			credentials: 'include',
		})
			.then(response => response.text())
			.then(response => {
				this.lastResponse = response;
				return response;
			});
	}

	applyCode(code, requestData) {
		requestData.params.set(this.REQUEST_PARAMS.COUPON_PARAM_NAME, code);
		requestData.params.set(this.REQUEST_PARAMS.ADD_COUPON_PARAM_NAME, this.REQUEST_PARAMS.ADD_COUPON_PARAM_VALUE);
		return this.manageCode(requestData).then(() => this._getPage());
	}

	removeCode(removeValueName, requestData) {
		requestData.params.set(this.DEFAULT_REMOVE_CODE_VALUE, this.REQUEST_PARAMS.REMOVE_COUPON_PARAM_VALUE);
		return this.manageCode(requestData).then(() => this._getPage());
	}
};

export default {
	name: 'ganderoutdoors',
	domain: 'ganderoutdoors.com',
	Driver,
	completionAction: completionActions.revisit,
	cartContinueUrl: document.querySelector('#cart-items-form') ? CART_PAGE_URL : `${BASE_SITE_URL}/billing`,
	isOnCartUrl(href) {
		return (
			document.querySelector(CODE_INPUT_SELECTOR) &&
			(href.includes('cart') || href.includes('payment') || href.includes('billing'))
		);
	},
};
