import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';

import { DOM, completionActions, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/**
Driver behavior:
   Existing code: yes
   Remove code: yes
   Code retesting: yes
   Stackable: yes
   Free gift support: no
   isValid implemented: no
   Show message about free shipping: no
   Driver is SPA: no, works on cart page when coupon form is present
   Gather merchant codes: no
   Special case: no
   Credentials: no
   Test Item(s): https://www.roxy.com/sunset-road-small-faux-leather-purse-192504045226.html
   Valid Code(s): PERKSPOT
 */

const CART_URL = 'https://www.roxy.com/cart';
const DEMANDWARE_CART_URL = 'https://www.roxy.com/on/demandware.store/Sites-RX-US-Site/en_US/Cart-SubmitForm';

const CURRENT_CART = window.location.href.includes('/cart') ? CART_URL : DEMANDWARE_CART_URL;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CODES_ELEMENTS_SELECTOR = '.cart__promoCodeList .detailscolumn';
		this.CART_FORM_SELECTOR = '#dwfrm_cart-tableform';
		this.CODE_VALUE_SELECTOR = '.cartcoupon .cartcoupon-code';
		this.TOTAL_SELECTOR = '.ordertotal .value';
		this.MERCHANT_CODES_SELECTOR = '.bx-wrap .coupon';

		this.PRICES_SELECTOR = '.total-set-row';
		this.PRICE_SELECTOR = '.total-set-row .value';
	}

	getMerchantCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.MERCHANT_CODES_SELECTOR)].filter(Boolean).map(el => el.textContent),
		);
		return [...codes];
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice, originalItemsCount } = validationInfo;

		// Sort by discount
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];
		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code, this._requestData())
				.then(() => this._getUpdatedCart())
				.then(html => {
					this.lastResponse = html;
					const finalPrice = this.parseTotal(html);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this.parseItemsCount(html) - originalItemsCount;

					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
						freeGifts,
					});
					if (
						finalPrice < currentPrice ||
						// Check that we don't increase total with free gifts code
						(finalPrice === currentPrice && freeGifts > currentFreeGifts)
					) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						currentFreeGifts = freeGifts;
						return Promise.resolve();
					}
					return this.removeCode(this._removeValueName(discount.code), this._requestData()).then(
						removeCodeHtml => {
							this.lastResponse = removeCodeHtml;
							return Promise.resolve();
						},
					);
				}),
		).then(() => {
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			});
			return Promise.resolve();
		});
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this._getUpdatedCart();
		const total = elemToFloat(extractElement(this.TOTAL_SELECTOR, cartStr));
		const shippingEl = this._findElement('Shipping', cartStr);
		const shipping = shippingEl ? elemToFloat(shippingEl) || 0 : 0;
		const taxEl = this._findElement('Tax', cartStr);
		const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
		return total - shipping - tax;
	}

	_getUpdatedCart() {
		return fetch(CART_URL, { credentials: 'include' }).then(response => response.text());
	}

	_findElement(targetString, response) {
		const targetEl = [...extractElement(this.PRICES_SELECTOR, response, true)].find(el =>
			el.textContent.includes(targetString),
		);
		return targetEl ? targetEl.querySelector(this.PRICE_SELECTOR) : null;
	}
};

export default {
	name: 'Roxy',
	domain: 'roxy.com',
	stackable: true,
	completionAction: completionActions.revisit,
	Driver,
	isOnCartUrl(href) {
		return document.querySelector('#dwfrm_cart_couponCode') && href.startsWith(CURRENT_CART);
	},
	cartContinueUrl: CART_URL,
};
