import DemandwareDriver from './_base';
import { extractElement } from '../../../utils/DOM';
import { elemToFloat } from '../../../utils/format';

/*
Driver behaviour description:
    Existing code: yes
    Remove code: yes
    Stackable: yes
    Free gift support: no
    'isValid' implemented: no
     Codes can be replaced by another code: no
    Manually apply code: no
    Is driver SPA and add the condition to show popup: no
Can run testing on the cart and deliveryinfo pages because on the addresses page after manual click user data will be erased
*/

const CART_URL = 'https://www.mattressfirm.com/cart';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.CODES_ELEMENTS_SELECTOR = '.rowcoupons, .coupon-applied-row';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
	}

	applyCode(code, requestData) {
		if (window.location.href.includes('deliveryinfo')) {
			return this._manageCode(
				`https://www.mattressfirm.com/on/demandware.store/Sites-Mattress-Firm-Site/default/Cart-AddCouponCheckoutJson?couponCode=${code}&format=ajax`,
			);
		}
		requestData.params.set(this.REQUEST_PARAMS.COUPON_PARAM_NAME, code);
		requestData.params.set(this.REQUEST_PARAMS.ADD_COUPON_PARAM_NAME, this.REQUEST_PARAMS.ADD_COUPON_PARAM_VALUE);
		return this.manageCode(requestData);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response));
			const calculatedShipping = shipping || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response));
			return this.parseTotal(response) - calculatedShipping - tax;
		});
	}

	_removeValueName(code) {
		const codesElements = extractElement(this.CODES_ELEMENTS_SELECTOR, this.lastResponse, true);
		const matchingCodesElement = [...codesElements].filter(element => {
			const codesElement = [...element.querySelectorAll(this.CODE_VALUE_SELECTOR)];
			let res = false;
			if (codesElement) {
				res = codesElement.find(el => el.innerText.trim() === code);
			}
			return !!res;
		});

		return matchingCodesElement.length
			? matchingCodesElement[0].querySelector(this.REMOVE_CODE_SELECTOR).getAttribute(this.REMOVE_VALUE_ATTRIBUTE)
			: this.DEFAULT_REMOVE_CODE_VALUE;
	}

	_manageCode(url) {
		return fetch(url)
			.then(() => fetch(window.location.href))
			.then(response => response.text());
	}

	_getUpdatedCart() {
		return fetch(CART_URL, {
			credentials: 'include',
			method: 'GET',
		}).then(response => response.text());
	}
};

export default {
	name: 'Mattress Firm',
	domain: 'mattressfirm.com',
	stackable: true,
	cartContinueUrl: window.location.href,
	Driver,
	maxCoupons: 10, // due to big total test time
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) || href.includes('deliveryinfo');
	},
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
};
