import Promise from 'bluebird';
import bus from '../../bus';
import DemandwareDriver from './_base';
import { format, DOM, completionActions, log } from '../../../utils';
import { sessionTimer } from '../../lib';

const { elemToFloat, toFloat } = format;
const { extractElement } = DOM;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+
+ Pages, where extension works:
    - cart
+ Is driver SPA: yes
+ Gather codes on site pages: no
*/

const CART_URL = 'https://www.tervis.com/cart';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.MANAGE_CODE_URL = 'https://www.tervis.com/on/demandware.store/Sites-Tervis-Site/default';
		this.TOTAL_SELECTOR = '.cart-total-price .grand-total';
		this.EXISTING_CODES_SELECTOR = '.coupons-and-promos .coupon-price-adjustment .coupon-code .remove-coupon';
		this.CODE_INPUT_SELECTOR = '#couponCode';

		this.TAX_SELECTOR = '.tax-total';
		this.SHIPPING_SELECTOR = '.shipping-cost';
		this.SHIPPING_DISCOUNT_SELECTOR = '.shipping-discount-total';

		this.TOKEN_SELECTOR = 'input[name="csrf_token"]';
		this.CODE_UUID_SELECTOR = '.coupons-and-promos .remove-coupon';

		this.isStackable = false;
	}

	checkCondition() {
		const onCart = window.location.href.startsWith(CART_URL);
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		return codeInput && onCart;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.EXISTING_CODES_SELECTOR)].map(el => el.getAttribute('data-code')),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this.removeCode(code))
			.then(() => this._getUpdatedCart())
			.then(responseHTML => this.parseTotal(responseHTML));
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log('originalPrice: %s', originalPrice);
		const originalItemsCount = this.parseItemsCount(this.lastResponse);
		log('originalItemsCount: %s', originalItemsCount);
		return Promise.each(codes, code =>
			this.applyCode(code, this._requestData()).then(json => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: false,
				};
				if (this._codeAppliedCheck(json)) {
					return bus.$emit('code-checked', result);
				}
				result.finalPrice = this.parseTotal(json);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this.parseItemsCount(json) - originalItemsCount;
				result.isValid = true;

				return this.removeCode(code).then(() => bus.$emit('code-checked', result));
			}),
		);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const tax = extractElement(this.TAX_SELECTOR, response);
			const shipping = extractElement(this.SHIPPING_SELECTOR, response);
			const shippingDiscount = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			return (
				this.parseTotal(response) -
				elemToFloat(tax) -
				elemToFloat(shipping) +
				toFloat(shippingDiscount ? shippingDiscount.textContent.replace('-', '') : 0)
			);
		});
	}

	applyCode(code) {
		const csrfToken = document.querySelector(this.TOKEN_SELECTOR).value;
		const applyQueryParams = new URLSearchParams({
			couponCode: code,
			csrf_token: csrfToken,
		});
		return this._manageCode(applyQueryParams, 'Cart-AddCoupon');
	}

	async removeCode(code) {
		const uuid = await this._getUUID();
		const removeQueryParams = new URLSearchParams({ code, uuid });
		return this._manageCode(removeQueryParams, 'Cart-RemoveCouponLineItem');
	}

	_manageCode(queryParams, action) {
		return fetch(`${this.MANAGE_CODE_URL}/${action}?${queryParams.toString()}`, {
			credentials: 'include',
			headers: {
				accept: 'application/json, text/javascript, */*; q=0.01',
				'x-requested-with': 'XMLHttpRequest',
			},
			method: 'GET',
			mode: 'cors',
		}).then(res => res.json());
	}

	_getUUID() {
		return this._getUpdatedCart().then(html =>
			extractElement(this.CODE_UUID_SELECTOR, html).getAttribute('data-uuid'),
		);
	}

	_codeAppliedCheck(data) {
		return data.errorMessage;
	}

	_getUpdatedCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	parseTotal(data) {
		if (typeof data === 'string') {
			const totalElement = extractElement(this.TOTAL_SELECTOR, data);
			return elemToFloat(totalElement);
		}
		return data ? toFloat(data.totals.grandTotal) : elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Tervis',
	domain: 'tervis.com',
	isSinglePageApp: true,
	completionAction: completionActions.revisit,
	Driver,
};
