import DemandwareDriver from './_base';
import { sessionTimer } from '../../lib';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: yes (only on sidebar)
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: sidebar, checkout(shipping)
+ Is driver SPA: yes
+ Gather codes on site pages: yes
+ sessionTimer: 15 min
*/

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.BASE_URL = 'https://www.coach.com/';
		this.TOTAL_SELECTOR = '.order-total .value, .sub-total-area .value';
		this.TOTAL_ELEMENT_CONFIG = {
			selector: this.TOTAL_SELECTOR,
		};
		this.CODE_VALUE_SELECTOR = '#promo-list';
		this.PRODUCT_ITEM_SELECTOR = '.product-info-wrap, .mini-cart-product';
		this.CART_ACTION_SELECTOR = '#cartAction';

		this.SIDEBAR_SELECTOR = '.cart-drawer';
		this.SIDEBAR_BUTTON_SELECTOR = '#miniCartContainer';

		this.INPUT_FIELD_SELECTOR = '#dwfrm_cart_couponCode';
		this.ERROR_MESSAGE_SELECTOR = '.error';
		this.CART_FORM_SELECTOR = '#dwfrm_cart';

		this.SHIPPING_SELECTOR = '.order-shipping span:last-child';
		this.TAX_SELECTOR = '.order-sales-tax span:last-child';

		this.isStackable = false;

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	checkCondition() {
		const popupEl = document.querySelector(this.SIDEBAR_SELECTOR);
		this.isOnPopupCart = popupEl ? popupEl.getAttribute('style').match(/visibility: inherit/) : false;

		const isOnShippingUrl = window.location.href.includes('shipping');
		const isRightUrl = window.location.href.startsWith(this.BASE_URL);
		const isInputField = document.querySelector(this.INPUT_FIELD_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		return isRightUrl && (this.isOnPopupCart || isOnShippingUrl) && isInputField && isTotal;
	}

	async beforeTestCodes() {
		const getCheckoutPage = await this._getPage(`${this.BASE_URL}/checkout`);
		this.lastResponse = getCheckoutPage;
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(element => element.value).filter(Boolean),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	beforeApplyCodes() {
		if (this.isOnPopupCart) {
			return sessionStorage.setItem('genieOpenSidebar', true);
		}
		return Promise.resolve();
	}

	manageCode(requestData) {
		const requestUrl = [extractElement(this.CART_ACTION_SELECTOR, this.lastResponse).value];
		return fetch(requestUrl, {
			method: 'POST',
			body: requestData.params,
			credentials: 'include',
			headers: this.REQUEST_HEADERS,
		}).then(response => response.text());
	}

	getPreTaxShippingTotal() {
		return this._getPage(`${this.BASE_URL}/checkout`).then(responseHTML => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, responseHTML)) || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, responseHTML)) || 0;
			return this.parseTotal(responseHTML) - shipping - tax;
		});
	}

	getUserData() {
		return true;
	}

	checkRestoreDataCondition() {
		return document.querySelector(this.SIDEBAR_BUTTON_SELECTOR);
	}

	setUserData() {
		const openSideBar = document.querySelector(this.SIDEBAR_BUTTON_SELECTOR);
		if (openSideBar) {
			openSideBar.click();
			sessionStorage.removeItem('genieOpenSidebar');
		}
	}

	_getPage(url) {
		return fetch(url).then(response => response.text());
	}
};

export default {
	name: 'Coach',
	domain: 'coach.com',
	stackable: false,
	Driver,
	isSinglePageApp: true,
	restoreUserData: true,
};
