import DemandwareDriver from './_base';
import { sessionTimer } from '../../lib';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.BASE_URL = 'duluthtrading.com';
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.CART_ITEM_SELECTOR = '.product-list-item';
		this.isStackable = false;
		this.CODE_VALUE_SELECTOR = '.js-coupon-code';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isOnCart =
			href.includes(`${this.BASE_URL}/cart/`) ||
			href.includes(`${this.BASE_URL}/on/demandware.store/Sites-DTC-Site/default/Cart-AddToWishlist`);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const item = document.querySelector(this.CART_ITEM_SELECTOR);
		return isOnCart && isTotal && item;
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(element => element.value.trim()),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}
};

export default {
	name: 'DuluthTrading',
	domain: 'duluthtrading.com',
	isSinglePageApp: true,
	Driver,
};
