import DemandwareDriver from './_base';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: can't use manual apply, because driver is stackable (we can't apply many codes manually)
+ Pages, where extension works: Cart (don't support Checkout, because clear user data)
+ Is driver SPA: no
+ Gather codes on site pages: yes, from Cart header
*/

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		const BASE_URL = 'https://www.juicycouturebeauty.com';
		this.CART_URL = `${BASE_URL}/cart`;
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.PRODUCT_ITEM_SELECTOR = '.cart-table-body .cart-row';
		this.ERROR_MESSAGE_SELECTOR = '.coupon-error';
		this.SHIPPING_SELECTOR = '.order-shipping .bfx-total-shipping';
		this.TAX_SELECTOR = '.order-sales-tax .bfx-total-tax';
		this.PROMO_SELECTOR = '.header-promotion';
		this.PROMO_REGEX = /code:?\s?(\w+)/i;
		this.lastResponse = null;
	}

	getMerchantCodes() {
		return [
			...new Set(
				[...document.querySelectorAll(this.PROMO_SELECTOR)]
					.map(el => el.textContent.match(this.PROMO_REGEX))
					.filter(Boolean)
					.map(el => el[1]),
			),
		];
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response));
			return this.parseTotal(response) - shipping - tax;
		});
	}

	_getUpdatedCart() {
		return fetch(this.CART_URL).then(res => res.text());
	}

	_codeAppliedCheck(data) {
		const errorEl = extractElement(this.ERROR_MESSAGE_SELECTOR, data);
		return errorEl && errorEl.textContent.match(/not be added/i);
	}
};

export default {
	name: 'Juicy Couture Beauty',
	domain: 'juicycouturebeauty.com',
	stackable: true,
	Driver,
	isOnCartUrl() {
		return document.querySelector('#dwfrm_cart_couponCode');
	},
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
};
