import Promise from 'bluebird';
import DemandwareDriver from './_base';
import { completionActions, DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { toInt } = format;

/** Behavior description:
 * - existing code: yes
 * - remove code: yes
 * - stackable: yes
 * - free gift support: yes
 * - 'isValid' implemented: no
 * - manually apply code: no
 * - replace code: true
 * */

const BASE_URL = 'https://www.legendarywhitetails.com';
const CART_URL = `${BASE_URL}/cart`;
const PAYMENT_URL = `${BASE_URL}/payment`;

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.PRODUCT_ITEM_SELECTOR = '.minicart-quantity';
		this.EXISTING_CODE_SELECTOR = '.coupon .success';
		this.EXISTING_CODE_REGEXP = /(code\s)(\w+)/i;
		this.IS_ON_CART = window.location.href.startsWith(CART_URL);
	}

	beforeTestCodes() {
		// we have to get Cart from Checkout page, because only on Cart removing codes is allowed
		if (!this.IS_ON_CART) {
			return this._getCart().then(response => {
				this.lastResponse = response;
				return Promise.resolve();
			});
		}
		return Promise.resolve();
	}

	getExistingCodes() {
		// we need different selector to get existing codes for Cart and Checkout
		const codeSelector = this.IS_ON_CART ? this.CODE_VALUE_SELECTOR : this.EXISTING_CODE_SELECTOR;
		const codes = new Set(
			[...document.querySelectorAll(codeSelector)].map(element =>
				this.IS_ON_CART
					? element.innerText.trim()
					: element.innerText.trim().match(this.EXISTING_CODE_REGEXP)[2],
			),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	parseItemsCount(html) {
		return toInt(extractElement(this.PRODUCT_ITEM_SELECTOR, html).textContent);
	}

	_getCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Legendary Whitetails',
	domain: 'legendarywhitetails.com',
	stackable: true,
	Driver,
	cartContinueUrl: window.location.href.startsWith(CART_URL) ? CART_URL : PAYMENT_URL,
	completionAction: completionActions.revisit,
	isOnCartUrl(href) {
		const inputElement = document.querySelector('#dwfrm_cart_couponCode, #dwfrm_billing_couponCode');
		return (
			(href.startsWith(CART_URL) || href.startsWith(PAYMENT_URL) || href.startsWith(`${BASE_URL}/on`)) &&
			inputElement
		);
	},
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
};
