import DemandwareAJAXDriver from './_base';
import completionActions from '../../../utils/completion-actions';
import { sessionTimer } from '../../lib';

/* Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    isValid implemented: no
    testing can be run on Cart page
    sessionTimer: 20min
Optional:
    Show message about free shipping on the cart page (without discount info): no
    Is driver SPA and add the condition to show popup: no
    Gather codes on site pages: no */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CODES_ELEMENTS_SELECTOR = '.item-list__coupon';
		this.CODE_VALUE_SELECTOR = '.coupon-radio label';
		this.TOTAL_SELECTOR = '.totals__container .item__price--large:last-child';

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 20,
		});
	}
};

export default {
	name: 'Charles Tyrwhitt',
	domain: 'ctshirts.com',
	stackable: true,
	completionAction: completionActions.revisit,
	Driver,
	isOnCartUrl(href) {
		return document.querySelector('#dwfrm_cart_couponCode') && href.startsWith('https://www.ctshirts.com/us/cart');
	},
};
