import DemandwareDriver from './_base';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
	}
};

export default {
	name: 'callawayapparel.com',
	domain: 'callawayapparel.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			href.startsWith('https://www.callawayapparel.com/basket') &&
			document.querySelector('#dwfrm_cart_couponCode')
		);
	},
};
