import DemandwareDriver from './_base';
import completionActions from '../../../utils/completion-actions';

/** Behavior:
 * Site isn't stackable (pretends to be, but remove request doesn't contain code value)
 * Code can be applied only on cart page
 */

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.APPLIED_CODES_SELECTOR = '.coupon-box .cartcoupon.clearfix';
		this.APPLIED_CODES_REGEX = /[a-zA-Z0-9]+/;

		this.isStackable = false;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(
			el => el.innerText.match(this.APPLIED_CODES_REGEX)[0],
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	applyCodes(codes) {
		window.history.replaceState(window.history.state, window.document.title, window.location.href.split('?')[0]);
		return this.applyCode(codes[0], this._requestData());
	}
};

export default {
	name: 'illy Shop',
	domain: 'illy.com',
	Driver,
	completionAction: completionActions.revisit, // sometimes website shows an alert
	isOnCartUrl(href) {
		return href.startsWith('https://www.illy.com/en-us/shop/cart') && document.querySelector('#promo-code');
	},
	isCartEmpty() {
		return !!document.querySelector('.cart-empty');
	},
};
