import DemandwareDriver from './_base';
import { sessionTimer } from '../../lib';
import { DOM, format } from '../../../utils';

const { extractFormValues, extractElement } = DOM;
const { elemToFloat } = format;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: cart, checkout
+ Is driver SPA: yes
+ Gather codes on site pages: yes
*/

const BASE_URL = 'https://www.narscosmetics.com/';
const CART_URL = `${BASE_URL}USA/cart`;

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-value';
		this.TOTAL_ELEMENT_CONFIG = {
			selector: this.TOTAL_SELECTOR,
		};
		this.PRODUCT_ITEM_SELECTOR = '.product-list-item';
		this.PRODUCT_ITEM_CHECKOUT_SELECTOR = '.mini-cart-product';
		this.TOKEN_SELECTOR = '[name="csrf_token"]';

		this.INPUT_FIELD_SELECTOR = '[name="dwfrm_cart_couponCode"]';
		this.ERROR_MESSAGE_SELECTOR = '.error';
		this.CART_FORM_SELECTOR = '#cart-items-form, [name="dwfrm_cart"]';

		this.MERCHANT_CODE_SELECTOR = '.promotion-bar-link';
		this.MERCHANT_CODE_REGEXP = /code (\w+)/i;

		this.SHIPPING_SELECTOR = '.order-shipping div:last-child';
		this.TAX_SELECTOR = '.order-sales-tax div:last-child';

		this.isStackable = false;

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	checkCondition() {
		const isInputField = document.querySelector(this.INPUT_FIELD_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		return isInputField && isTotal;
	}

	getMerchantCodes() {
		return this._getPage(BASE_URL).then(responseHTML => {
			return [...extractElement(this.MERCHANT_CODE_SELECTOR, responseHTML, true)]
				.map(elem => elem.innerText.match(this.MERCHANT_CODE_REGEXP))
				.filter(Boolean)
				.map(elem => elem[1]);
		});
	}

	async getExistingCodes() {
		const getCartPage = await this._getPage(CART_URL);
		const codes = new Set(
			[...extractElement(this.CODE_VALUE_SELECTOR, getCartPage, true)].map(element => element.innerText.trim()),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	getPreTaxShippingTotal() {
		return this._getPage(CART_URL).then(responseHTML => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, responseHTML)) || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, responseHTML)) || 0;
			return this.parseTotal(responseHTML) - shipping - tax;
		});
	}

	parseItemsCount(html) {
		const cartItems = document.querySelector(this.PRODUCT_ITEM_SELECTOR);
		return cartItems
			? extractElement(this.PRODUCT_ITEM_SELECTOR, html, true).length
			: extractElement(this.PRODUCT_ITEM_CHECKOUT_SELECTOR, html, true).length;
	}

	_requestData() {
		// Token is not present in the form, need to add it manually to use template
		const csrfTokenElement = extractElement(this.TOKEN_SELECTOR, this.lastResponse);
		const formValues = extractFormValues(this.CART_FORM_SELECTOR, this.lastResponse);
		formValues.params.set('csrf_token', csrfTokenElement.value);
		return formValues;
	}

	_getPage(url) {
		return fetch(url).then(response => response.text());
	}
};

export default {
	name: 'NARS',
	domain: 'narscosmetics.com',
	stackable: false,
	Driver,
	isSinglePageApp: true,
};
