import DemandwareDriver from './_base';
import { format, DOM, browserUtils } from '../../../utils';
import { sessionTimer } from '../../lib';

const { elemToFloat } = format;
const { extractElement } = DOM;
const { wait } = browserUtils;

const BASE_SITE_URL = 'https://www.thelimited.com';
const CART_URL = `${BASE_SITE_URL}/my-bag`;

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.SHIPPING_SELECTOR = '.order-shipping td:nth-child(2)';
		this.MERCHANT_CODE_SELECTOR = '[id*=monetate_selectorHTML] .c_text';
		this.MERCHANT_CODE_REGEXP = /with code\s(\w+)/i;
		sessionTimer.initialize({});
	}

	async getMerchantCodes() {
		const iframeElem = document.createElement('iframe');
		iframeElem.src = `${BASE_SITE_URL}/customer-service/coupons-promotions/`;
		Object.assign(iframeElem.style, { display: 'none' });
		document.documentElement.appendChild(iframeElem);

		const getCode = () => iframeElem.contentDocument.querySelector(this.MERCHANT_CODE_SELECTOR);
		await wait(getCode, 10000, 300, true);
		return getCode() ? [getCode().innerText.match(this.MERCHANT_CODE_REGEXP)[1]] : [];
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			// no taxes on the cart page
			const shippingEl = extractElement(this.SHIPPING_SELECTOR, response);
			const shipping = shippingEl.textContent.toLowerCase().trim() === 'free' ? 0 : elemToFloat(shippingEl);
			return this.parseTotal(response) - shipping;
		});
	}

	_getUpdatedCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'The Limited',
	domain: 'thelimited.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('#dwfrm_cart_couponCode');
	},
};
