import DemandwareDriver from './_base';
import { format, DOM } from '../../../utils';

const { toFloat } = format;
const { extractData, extractFormValues, extractElement } = DOM;

/*
Behavior description:
- can get existing codes;
- can remove codes;
- valid code can't be replaced with another valid code;
- can apply multiple codes (stackable);
- code testing may be run on 'Cart' page;

- tax does not add to the total price on the cart page,
- but when a user  goes  after cart to the checkout page and
- comes again to cart page tax will be added to the total price
*/

const BASE_URL = 'https://www.blu.com';
const CART_URL = `${BASE_URL}/en/US/cart`;

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.CART_EMPTY_SELECTOR = '.cart-empty';
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode';
		this.ERROR_MESSAGE_SELECTOR = '#add-coupon+.error';
		this.MERCHANT_CODES_SELECTOR = '[data-testid="desktop-banner-section-body"] p strong';
	}

	checkCondition() {
		const isOnRightUrl = window.location.href.includes(CART_URL);
		const isCartEmpty = document.querySelector(this.CART_EMPTY_SELECTOR);
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		return isOnRightUrl && !isCartEmpty && codeInput;
	}

	getMerchantCodes() {
		return this._getPage().then(responseHTML => {
			return [
				...new Set(
					[...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
						.map(elem => elem.textContent.trim())
						.filter(Boolean),
				),
			];
		});
	}

	// need to do this to get original price
	beforeCheckCodes() {
		return this.applyCode('fake_code', this._requestData()).then(response => {
			this.lastResponse = response;
		});
	}

	_requestData() {
		const formValues = extractFormValues(this.CART_FORM_SELECTOR, this.lastResponse);
		if (formValues) {
			this.requestData = formValues;
		} else {
			// If last response doesn't contain form,
			// parse current document
			this.requestData = extractFormValues(this.CART_FORM_SELECTOR, null);
		}
		return this.requestData;
	}

	_removeValueName(code) {
		const codesElements = extractElement(this.CODES_ELEMENTS_SELECTOR, this.lastResponse, true);
		const matchingCodesElement = [...codesElements].find(element => {
			const codesElement = element.querySelector(this.CODE_VALUE_SELECTOR);
			return codesElement && codesElement.innerText.toLowerCase().trim() === code.toLowerCase();
		});
		return matchingCodesElement
			? matchingCodesElement.querySelector(this.REMOVE_CODE_SELECTOR).getAttribute(this.REMOVE_VALUE_ATTRIBUTE)
			: this.DEFAULT_REMOVE_CODE_VALUE;
	}

	_getPage() {
		return fetch(BASE_URL).then(response => response.text());
	}

	parseTotal(html) {
		return toFloat(extractData(this.TOTAL_SELECTOR, html));
	}
};

export default {
	name: 'Blu',
	domain: 'blu.com',
	isSinglePageApp: true,
	maxCoupons: 10, // site is slow
	cartContinueUrl: CART_URL, // after removing code manually URL address is changed so we need change URL
	stackable: true,
	Driver,
};
