import DemandwareDriver from './_base';

/*
Behavior description:
- can get existing codes;
- can remove codes;
- valid code can be replaced with another valid code;
- can't apply multiple codes (not stackable);
- code testing may be run on 'Cart', 'Shipping', 'Payment', 'Review' pages;
*/

const BASE_URL = 'https://www.tatcha.com';
const EDIT_PAYMENT_URL = `${BASE_URL}/checkout/editpayment`;
const REVIEW_URL = `${BASE_URL}/revieworder`;
const IS_ON_PAYMENT = window.location.pathname.includes('/checkout/payment');
const IS_ON_REVIEW = window.location.pathname.includes('/checkout/review');

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.checkout-summary-total td:last-child';
		this.APPLIED_CODES_SELECTOR = '.promoapply, #promoCode';

		this.CODE_INPUT_SELECTOR = '#promoCode';
		this.APPLY_BUTTON_SELECTOR = '[name="dwfrm_cart_addCoupon"]';

		// the check where a user on shipping or payment pages
		this.IS_ON_SHIPPING_PAYMENT = [
			'/checkout/payment',
			'/checkout/editpayment',
			'/checkout/shipping',
			'/checkout/review',
			'/revieworder',
		].some(str => window.location.pathname.includes(str));
		this.CART_FORM_SELECTOR = '#cart-coupon-form, #cart-items-form';
		this.PRODUCT_ITEM_SELECTOR = this.IS_ON_SHIPPING_PAYMENT ? '.summary-bag-item' : '.bag-item-line';
		this.isStackable = false;

		this.APPLY_URL = `${BASE_URL}/on/demandware.store/Sites-tatcha-Site/default/Cart-AddCouponJson`;
		this.REMOVE_URL = `${BASE_URL}/on/demandware.store/Sites-tatcha-Site/default/Cart-RemoveCoupon`;

		this.BILLING_ADDRESS_SELECTOR = '.radio-billing-address.selected';
		this.SHIPPING_ADDRESS_SELECTOR = '.checkout-billing-address';
		this.EMPTY_CART_SELECTOR = '.cart-empty.bag-is-empty';
		this.SPINNER_ONE_SELECTOR = '.loader-fullpage';
		this.SPINNER_TWO_SELECTOR = '.sk-fading-circle';
		this.REMOVE_BUTTON_SELECTOR = '.bag-remove';
		this.MODAL_SELECTOR = '.modal';
		this.PROMO_ITEM_ELEMENT = '.bag-item-promo';
	}

	checkCondition() {
		const { href } = window.location;
		const isOnRightUrl =
			href.startsWith(`${BASE_URL}/bag`) ||
			href.startsWith(`${BASE_URL}/on/demandware.store/Sites-tatcha-Site/default/Cart-SubmitForm`) ||
			href.startsWith(`${BASE_URL}/checkout/payment`) ||
			href.startsWith(`${BASE_URL}/checkout/editpayment`) ||
			href.startsWith(`${BASE_URL}/checkout/review`) ||
			href.startsWith(`${BASE_URL}/revieworder`) ||
			// in this URL we show popup only for logged user
			(href.startsWith(`${BASE_URL}/checkout/shipping`) && document.querySelector(this.BILLING_ADDRESS_SELECTOR));
		const isForm = document.querySelector(this.CART_FORM_SELECTOR);
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const spinnerElementOne = document.querySelector(this.SPINNER_ONE_SELECTOR);
		const spinnerElementTwo = document.querySelector(this.SPINNER_TWO_SELECTOR);
		const isSpinnerOneActive = spinnerElementOne ? spinnerElementOne.style.display !== 'none' : false;
		const isSinnerTwoActive = spinnerElementTwo && spinnerElementTwo.style.display !== 'none';
		const isModalActive = [...document.querySelectorAll(this.MODAL_SELECTOR)].some(
			el => el.style.display === 'block',
		);
		const isSippingFieldsOpen = !(document.querySelector(this.SHIPPING_ADDRESS_SELECTOR) || {}).offsetParent;
		const itemElements = document.querySelectorAll(`${this.PRODUCT_ITEM_SELECTOR}:not(${this.PROMO_ITEM_ELEMENT})`);
		const itemElemCount = itemElements ? itemElements.length > 0 : false;
		return (
			isOnRightUrl &&
			isForm &&
			!isSpinnerOneActive &&
			!emptyCart &&
			!isSinnerTwoActive &&
			!isModalActive &&
			isSippingFieldsOpen &&
			itemElemCount
		);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(el => el.value).filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	applyCode(code, requestData) {
		if (this.IS_ON_SHIPPING_PAYMENT) {
			requestData.params.set('couponCode', code);
			requestData.params.set(this.REQUEST_PARAMS.ADD_COUPON_PARAM_NAME, 'Apply');
			requestData.params.delete(this.REQUEST_PARAMS.COUPON_PARAM_NAME);
			requestData.action = this.APPLY_URL;
		} else {
			requestData.params.set(this.REQUEST_PARAMS.COUPON_PARAM_NAME, code);
			requestData.params.set(
				this.REQUEST_PARAMS.ADD_COUPON_PARAM_NAME,
				this.REQUEST_PARAMS.ADD_COUPON_PARAM_VALUE,
			);
		}
		return this.manageCode(requestData);
	}

	removeCode(removeValueName, requestData) {
		if (this.IS_ON_SHIPPING_PAYMENT) {
			requestData.params.delete(removeValueName);
			requestData.params.set('null', 'Remove');
			requestData.action = this.REMOVE_URL;
		} else {
			requestData.params.set(removeValueName, this.REQUEST_PARAMS.REMOVE_COUPON_PARAM_VALUE);
		}
		return this.manageCode(requestData);
	}

	manageCode(requestData) {
		const url = `${
			this.IS_ON_SHIPPING_PAYMENT ? `${requestData.action}?${requestData.params}` : requestData.action
		}`;
		return fetch(url, {
			method: this.IS_ON_SHIPPING_PAYMENT ? 'GET' : 'POST',
			body: this.IS_ON_SHIPPING_PAYMENT ? null : requestData.params,
			credentials: 'include',
			headers: this.REQUEST_HEADERS,
		}).then(response => response.text());
	}
};

// need to set right URL, to site works correctly
const _continueUrl = () => {
	if (IS_ON_PAYMENT) {
		return EDIT_PAYMENT_URL;
	}
	if (IS_ON_REVIEW) {
		return REVIEW_URL;
	}
	return window.location.href;
};

export default {
	name: 'Tatcha',
	domain: 'tatcha.com',
	maxCoupons: 12, // site is slow
	Driver,
	isSinglePageApp: true,
	// we need to do this because unnecessary browser alert appears on the page
	cartContinueUrl: _continueUrl(),
	isOnCartUrl() {
		return false;
	},
};
