import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { DOM, format, log } from '../../../utils';

const { elemToFloat } = format;
const { extractFormValues, extractElement } = DOM;

export default class DemandwareDriver extends BaseDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total td:last-child';
		this.PRODUCT_ITEM_SELECTOR = null;

		this.CODES_ELEMENTS_SELECTOR = '.rowcoupons';
		this.CODE_VALUE_SELECTOR = '.cartcoupon .value';
		this.CART_FORM_SELECTOR = '#cart-items-form';

		this.DEFAULT_REMOVE_CODE_VALUE = 'dwfrm_cart_coupons_i0_deleteCoupon';
		this.REMOVE_CODE_SELECTOR = '[name*="_deleteCoupon"]';
		this.REMOVE_VALUE_ATTRIBUTE = 'name';

		this.REQUEST_HEADERS = {
			Accept: 'text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8',
			'Content-Type': 'application/x-www-form-urlencoded',
		};
		this.REQUEST_PARAMS = {
			COUPON_PARAM_NAME: 'dwfrm_cart_couponCode',
			ADD_COUPON_PARAM_NAME: 'dwfrm_cart_addCoupon',
			ADD_COUPON_PARAM_VALUE: 'dwfrm_cart_addCoupon',
			REMOVE_COUPON_PARAM_VALUE: 'Remove',
		};
		this.ERROR_MESSAGE_SELECTOR = null;
		this.isStackable = true;
	}

	applyCodes(codes) {
		return this.isStackable ? Promise.resolve() : this.applyCode(codes[0], this._requestData());
	}

	checkCodes(codes) {
		const originalPrice = this.parseTotal(this.lastResponse);
		log('originalPrice: %s', originalPrice);
		const originalItemsCount = this.parseItemsCount(this.lastResponse);
		log('originalItemsCount: %s', originalItemsCount);
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code, this._requestData()).then(html => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: false,
				};
				if (this._codeAppliedCheck(html)) {
					return bus.$emit('code-checked', result);
				}
				this.lastResponse = html;
				result.finalPrice = this.parseTotal(html);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this.parseItemsCount(html) - originalItemsCount;
				result.isValid = true;

				if (
					result.finalPrice < originalPrice ||
					// Free gifts code is acceptable if it won't increase total
					(result.finalPrice === originalPrice && result.freeGifts > 0)
				) {
					workingDiscounts.push(result);
				}

				return this.removeCode(this._removeValueName(code), this._requestData()).then(removeCodeHtml => {
					this.lastResponse = removeCodeHtml;
					return bus.$emit('code-checked', result);
				});
			}),
		).then(() => {
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			});
			return Promise.resolve();
		});
	}

	beforeTestCodes() {
		this.lastResponse = null;
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(element => element.innerText.trim()),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code =>
			this.removeCode(this._removeValueName(code), this._requestData()).then(html => {
				this.lastResponse = html;
				return Promise.resolve();
			}),
		);
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice, originalItemsCount } = validationInfo;

		// Sort by discount
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];
		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code, this._requestData()).then(html => {
				this.lastResponse = html;
				const finalPrice = this.parseTotal(html);
				const finalDiscount = originalPrice - finalPrice;
				const freeGifts = this.parseItemsCount(html) - originalItemsCount;

				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
					freeGifts,
				});
				if (
					finalPrice < currentPrice ||
					// Check that we don't increase total with free gifts code
					(finalPrice === currentPrice && freeGifts > currentFreeGifts)
				) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					currentFreeGifts = freeGifts;
					return Promise.resolve();
				}
				return this.removeCode(this._removeValueName(discount.code), this._requestData()).then(
					removeCodeHtml => {
						this.lastResponse = removeCodeHtml;
						return Promise.resolve();
					},
				);
			}),
		).then(() => {
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			});
			return Promise.resolve();
		});
	}

	_requestData() {
		return extractFormValues(this.CART_FORM_SELECTOR, this.lastResponse);
	}

	_removeValueName(code) {
		const codesElements = extractElement(this.CODES_ELEMENTS_SELECTOR, this.lastResponse, true);
		const matchingCodesElement = [...codesElements].find(element => {
			const codesElement = element.querySelector(this.CODE_VALUE_SELECTOR);
			return codesElement && codesElement.innerText.trim() === code;
		});

		return matchingCodesElement
			? matchingCodesElement.querySelector(this.REMOVE_CODE_SELECTOR).getAttribute(this.REMOVE_VALUE_ATTRIBUTE)
			: this.DEFAULT_REMOVE_CODE_VALUE;
	}

	manageCode(requestData) {
		return fetch(requestData.action, {
			method: 'POST',
			body: requestData.params,
			credentials: 'include',
			headers: this.REQUEST_HEADERS,
		}).then(response => response.text());
	}

	applyCode(code, requestData) {
		requestData.params.set(this.REQUEST_PARAMS.COUPON_PARAM_NAME, code);
		requestData.params.set(this.REQUEST_PARAMS.ADD_COUPON_PARAM_NAME, this.REQUEST_PARAMS.ADD_COUPON_PARAM_VALUE);
		return this.manageCode(requestData);
	}

	removeCode(removeValueName, requestData) {
		requestData.params.set(removeValueName, this.REQUEST_PARAMS.REMOVE_COUPON_PARAM_VALUE);
		return this.manageCode(requestData);
	}

	parseTotal(html) {
		const totalElement = extractElement(this.TOTAL_SELECTOR, html);
		return elemToFloat(totalElement);
	}

	parseItemsCount(html) {
		return this.PRODUCT_ITEM_SELECTOR ? extractElement(this.PRODUCT_ITEM_SELECTOR, html, true).length : 0;
	}

	_codeAppliedCheck(data) {
		return this.ERROR_MESSAGE_SELECTOR ? extractElement(this.ERROR_MESSAGE_SELECTOR, data) : false;
	}
}
