import DemandwareDriver from './_base';
import { completionActions, DOM, format } from '../../../utils';
import { sessionTimer } from '../../lib';

const { extractData, extractElement } = DOM;
const { elemToFloat } = format;

const IS_PLUS_SUBDOMAIN = window.location.href.includes('plus');
const BASE_URL = `https://${IS_PLUS_SUBDOMAIN ? 'plus' : 'www'}.joann.com`;
const CART_URL = `${BASE_URL}/cart`;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: yes
+ Gather codes on site pages: yes, from https://www.joann.com/coupon/
+ Session timer: 20 min
+ maxCoupons: 10
*/

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.isStackable = false;
		this.TOTAL_SELECTOR = '.cart__totals__row--total span:not(.cart__totals__label)';
		this.PRICES_SELECTOR = '.cart__totals__row';
		this.PRICE_SELECTOR = 'span:last-child';
		this.CART_EMPTY_SELECTOR = '.mini-cart-empty';
		this.CART_FORM_SELECTOR = '#cart-items-form';
		this.PRODUCT_ITEM_SELECTOR = '.cart__product';
		this.ERROR_MESSAGE_SELECTOR = '.cart-coupon-code .error';
		this.MERCHANTS_CODES_SELECTOR = '.coupon__promo-code';
		this.EMPTY_CART_REGEX = /is empty/i;
		this.SESSION_TIMEOUT = 20;
	}

	watchForCartUrl() {
		sessionTimer.initialize({
			actionInterval: super.watchForCartUrl(),
			timeout: this.SESSION_TIMEOUT,
		});
	}

	checkCondition() {
		const isCartEmpty = document.querySelector(this.CART_EMPTY_SELECTOR);
		const cartForm = document.querySelector(this.CART_FORM_SELECTOR);
		const isCart =
			(window.location.href.match(/\/cart/i) || window.location.href.match('/my-account-home')) && cartForm;

		const emptyCart = isCartEmpty || (cartForm && cartForm.textContent.match(this.EMPTY_CART_REGEX));

		return !emptyCart && isCart;
	}

	getMerchantCodes() {
		if (!IS_PLUS_SUBDOMAIN) {
			return this._getUpdatedPage('https://www.joann.com/coupon/', true).then(resHtml => [
				...new Set(
					[...extractElement(this.MERCHANTS_CODES_SELECTOR, resHtml, true)]
						.map(el => el.innerText.trim())
						.filter(Boolean),
				),
			]);
		}
		return [];
	}

	getExistingCodes() {
		return this._getUpdatedPage().then(responseText => {
			const codes = new Set(extractData(this.CODE_VALUE_SELECTOR, responseText, true).map(code => code.trim()));
			return {
				codes: [...codes],
				codeCount: codes.size,
			};
		});
	}

	beforeCheckCodes() {
		return this._getUpdatedPage().then(responseText => {
			this.lastResponse = responseText;
		});
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedPage().then(response => {
			const shipping = elemToFloat(this._findElement('Shipping', response)) || 0;
			const tax = elemToFloat(this._findElement('Tax', response)) || 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}

	_findElement(targetString, response) {
		const targetEl = [...extractElement(this.PRICES_SELECTOR, response, true)].find(el =>
			el.textContent.includes(targetString),
		);
		return targetEl ? targetEl.querySelector(this.PRICE_SELECTOR) : null;
	}

	_getUpdatedPage(url = CART_URL, retry = false) {
		return fetch(url, {
			method: 'GET',
			credentials: 'include',
		})
			.then(response => response.text())
			.then(response => {
				if (!extractElement(this.TOTAL_SELECTOR, response) && !retry) {
					return this._getUpdatedPage(CART_URL, true);
				}
				return response;
			});
	}
};

export default {
	name: 'Joann',
	domain: 'joann.com',
	completionAction: completionActions.revisit,
	isSinglePageApp: true,
	cartContinueUrl: CART_URL,
	maxCoupons: 10, // site works slow
	Driver,
};
