import Promise from 'bluebird';
import DemandwareDriver from './_base';
import bus from '../../bus';
import { completionActions, DOM, format, log } from '../../../utils';
import { sessionTimer, handleDriverError } from '../../lib';

const { toFloat, elemToFloat } = format;
const { extractElement, extractData } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ SessionTimer: 10min
 */

const CART_URL = 'https://www.luckybrand.com/cart';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.CODE_VALUE_SELECTOR = '.rowcoupons.cart-row .product-list-item div:nth-of-type(1) .value';
		this.CART_FORM_SELECTOR = 'div.cart-coupon-code > form';
		this.CART_COUPON_SELECTOR = '.cart-coupon-code';
		this.CART_CONTAINER_SELECTOR = '.cart-main-container';

		this.isStackable = false;

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-of-type';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.ERROR_MESSAGE_SELECTOR = '#open-promo .error';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 10,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isOnRightUrl = href.startsWith(CART_URL) || href.includes('Cart-SubmitForm');
		const cartCouponEl = document.querySelector(this.CART_COUPON_SELECTOR);
		return isOnRightUrl && cartCouponEl;
	}

	checkCodes(codes) {
		const originalPrice = this.parseTotal(this.lastResponse);
		log('originalPrice: %s', originalPrice);
		const originalItemsCount = this.parseItemsCount(this.lastResponse);
		log('originalItemsCount: %s', originalItemsCount);
		return Promise.each(codes, code =>
			this.applyCode(code, this._requestData()).then(html => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: false,
				};
				if (this._codeAppliedCheck(html)) {
					// need to remove even invalid codes, because some of them still to be present on the page
					return this.removeCode(this._removeValueName(code), this._requestData()).then(() =>
						bus.$emit('code-checked', result),
					);
				}
				this.lastResponse = html;
				result.finalPrice = this.parseTotal(html);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this.parseItemsCount(html) - originalItemsCount;
				result.isValid = true;

				return this.removeCode(this._removeValueName(code), this._requestData()).then(removeCodeHtml => {
					this.lastResponse = removeCodeHtml;
					return bus.$emit('code-checked', result);
				});
			}),
		);
	}

	async getPreTaxShippingTotal() {
		const responseHTML = await this._getUpdatedCart();
		let total = 0;
		try {
			total = this.parseTotal(responseHTML);
		} catch (TypeError) {
			const orderSectionEl = extractElement(this.CART_CONTAINER_SELECTOR, responseHTML);
			const pageSection = orderSectionEl && orderSectionEl.innerHTML;
			handleDriverError(new Error('Invalid reponse without total'), { errorPayload: pageSection });
			return null;
		}
		const shipping = toFloat(extractData(this.SHIPPING_SELECTOR, responseHTML)) || 0;
		const tax = toFloat(extractData(this.TAX_SELECTOR, responseHTML)) || 0;

		const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, responseHTML);
		const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;

		return total - shipping - tax + shippingDiscount;
	}

	removeCode(removeValueName, requestData) {
		requestData.params.set('dwfrm_cart_updateCart', 'dwfrm_cart_updateCart');
		requestData.params.set(removeValueName, this.REQUEST_PARAMS.REMOVE_COUPON_PARAM_VALUE);

		return this.manageCode(requestData);
	}

	_getUpdatedCart() {
		return fetch(CART_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Lucky Brand',
	domain: 'luckybrand.com',
	Driver,
	cartContinueUrl: CART_URL,
	isSinglePageApp: true,
	completionAction: completionActions.revisit,
};
