import DemandwareDriver from './_base';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
	}

	beforeApplyCodes() {
		window.history.pushState(window.history.state, window.document.title, window.location.href.split('?')[0]);
		return Promise.resolve();
	}
};

export default {
	name: 'Original Penguin',
	domain: 'originalpenguin.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			href.includes('https://www.originalpenguin.com/basket') && document.querySelector('#dwfrm_cart_couponCode')
		);
	},
};
