import DemandwareDriver from './_base';
import { sessionTimer } from '../../lib';

const CART_URL = 'https://www.dickies.com/cart';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}
};

export default {
	name: 'Dickies',
	domain: 'dickies.com',
	stackable: true,
	cartContinueUrl: CART_URL,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('[name="dwfrm_cart_couponCode"]');
	},
};
