import Promise from 'bluebird';
import DemandwareDriver from './_base';
import bus from '../../bus';
import { browserUtils, DOM, format, completionActions } from '../../../utils';

const { getCookie } = browserUtils;
const { extractElement } = DOM;
const { elemToFloat } = format;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: yes, cart page
*/

const BASE_URL = 'https://www.quiksilver.com';

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.CART_FORM_SELECTOR = '#dwfrm_cart-tableform';
		this.CODE_VALUE_SELECTOR = '.cartcoupon-code';
		this.TOTAL_SELECTOR = '.ordertotal td:last-child';
		this.VALUE_SELECTOR = '.total-value-container > div';
		this.INDEX_SELECTOR = '.total-label-container > div';
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode';
		this.PROMO_CODE_SELECTOR = '.coupon';
		this.MERCHANT_CODE_SELECTOR = '.unslider-carousel li';
		this.MERCHANT_CODE_REGEX = /use code:? ? ?(\w+)/i;
		this.PRICES_SELECTOR = '.total-set-row';
		this.PRICE_SELECTOR = '.value';
	}

	checkCondition() {
		const { href } = window.location;
		const isOnRightUrl = href.includes(`${BASE_URL}/cart`) || href.includes(`Cart-SubmitForm`);
		const shipToUs = getCookie('ShipToCountry') === 'US';
		const couponCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		return isOnRightUrl && couponCodeInput && isTotal && shipToUs && document.readyState === 'complete';
	}

	getMerchantCodes() {
		const individualMerchantCode = [document.querySelector(this.PROMO_CODE_SELECTOR)]
			.filter(Boolean)
			.map(el => el.textContent);
		const merchantCode = [
			...new Set(
				[...document.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
					.map(item => item.textContent.match(this.MERCHANT_CODE_REGEX))
					.filter(Boolean)
					.map(item => item[1]),
			),
		];
		return [...individualMerchantCode, ...merchantCode];
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice, originalItemsCount } = validationInfo;

		// Sort by discount
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];
		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code, this._requestData()).then(html => {
				this.lastResponse = html;
				const finalPrice = this.parseTotal(html);
				const finalDiscount = originalPrice - finalPrice;
				const freeGifts = this.parseItemsCount(html) - originalItemsCount;

				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
					freeGifts,
				});
				if (
					finalPrice < currentPrice ||
					// Check that we don't increase total with free gifts code
					(finalPrice === currentPrice && freeGifts > currentFreeGifts)
				) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					currentFreeGifts = freeGifts;
					return Promise.resolve();
				}
				return this.removeCode(this._removeValueName(discount.code), this._requestData()).then(
					removeCodeHtml => {
						this.lastResponse = removeCodeHtml;
						return Promise.resolve();
					},
				);
			}),
		).then(() => {
			return Promise.each(successfulCodes, code => this.applyCode(code, this._requestData())).then(() => {
				bus.$emit('stackable-complete', {
					codes: successfulCodes,
					finalPrice: currentPrice,
					finalDiscount: currentDiscount,
					freeGifts: currentFreeGifts,
				});
				return Promise.resolve();
			});
		});
	}

	getPreTaxShippingTotal() {
		return fetch(`${BASE_URL}/cart/`, {
			method: 'GET',
		})
			.then(res => res.text())
			.then(responseHTML => {
				const taxEl = this._findElement('Tax', responseHTML);
				const tax = taxEl ? elemToFloat(taxEl) : 0;
				const shippingEl = this._findElement('Shipping', responseHTML);
				const shipping = shippingEl ? elemToFloat(shippingEl) || 0 : 0;
				return this.parseTotal(responseHTML) - shipping - tax;
			});
	}

	_findElement(targetString, response) {
		const targetEl = [...extractElement(this.PRICES_SELECTOR, response, true)].find(el =>
			el.textContent.includes(targetString),
		);
		return targetEl ? targetEl.querySelector(this.PRICE_SELECTOR) : null;
	}
};

export default {
	name: 'Quiksilver',
	domain: 'quiksilver.com',
	cartContinueUrl: `${BASE_URL}/cart`,
	stackable: true,
	completionAction: completionActions.revisit,
	Driver,
	isSinglePageApp: true,
	showDifferentDiscountMessage: true,
};
